const singlefySlash = pathName => pathName.split("/").filter(s => s).join("/");

export const homeTypesLinksMap = types.map(type => `/types/${singlefySlash(type)}`);

export const tourismTypesLinksMap = types.map(type => `${linksMap.tourism}/types/${singlefySlash(type)}`);

type IProductTypes = {
  [key: string]: { name: string, url: string, searchName: string };
};


export const productTypes: IProductTypes = {
  [linksMap.tourism]: {
    name: 'הכל',
    url: linksMap.tourism,
  },
  [linksMap.cruises]: {
    name: 'הפלגות',
    searchName: 'חברת הפלגות',
  },
  [linksMap.flights]: {
    name: 'טיסות',
    searchName: 'חברת תעופה',
  },
  [linksMap.vacations]: {
    name: 'חבילות',
    searchName: 'סוכנות נסיעות',
  },
  [linksMap.hotels]: {
    name: 'מלונות',
    searchName: 'שם מלון',
  },
} as any;
for (const type in productTypes) {
  if (!productTypes[type].url) {
    productTypes[type].url = `${linksMap.tourism}/types/${singlefySlash(type)}`;
  }
}

const tourismPattern = new UrlPattern(linksMap.tourismTypes);

export const getTourismType = () => (tourismPattern.match(location.pathname) || {}).type;
