import React from 'react';
import {IHotelDetails} from 'gui/components/editors/hotelEditor/components/hotelDetailsForm/components/hotelDetails';
import {FormHelperText, Typography} from '@material-ui/core';
import {When} from 'react-if';
import styles from './PlaceImagePicker.module.scss';
import Picker from './components/picker/Picker';
import {useWindowWidth,} from '@react-hook/window-size'
import {withSize} from 'react-sizeme';
import SkeletonPlaceHolder, {eSkeletonVariant} from 'gui/components/skeletonPlaceHolder';

interface IPlaceImagePickerProps {
  name?: string;
  values?: IHotelDetails;
  submitCount?: any;
  errors?: any;
  setFieldValue?: any;
  size?: { width: number, height: number },
}

const imagesCount = (props) => `${props.values[props.name].length}/${props.values.hotel.photos.length}`;
const PlaceImagePicker = (props: IPlaceImagePickerProps) => {
  const width = useWindowWidth();
  const error = props.submitCount > 0 && props.errors[props.name];
  return (
    <div className={`${styles.PlaceImagePicker} ${(error ? `${styles.error} ` : "")}`}>
      <Typography variant="h6" component="h6" className={styles.topTitle}>
        תמונות מתוך גוגל(מומלץ לבחור תמונות איכותיות)
      </Typography>
      <input name={props.name} type="hidden"/>
      <When
        condition={!!error}>{() => <FormHelperText
        className={styles.error}
        error>{props.errors[props.name]}
      </FormHelperText>}</When>
      <Picker {...props} key={width}/>
      <p className={styles.imagesElected}>
        {props.values.hotel.loading ?
          <SkeletonPlaceHolder variant={eSkeletonVariant.text} width={100}/> : imagesCount(props)}
      </p>
    </div>
  );
};

PlaceImagePicker.defaultProps = {
  name: "imagePick"
  //sizes: {width: width}
};

export default withSize()(React.memo(PlaceImagePicker));
