import React from "react";
import FormikCustomInput from '../formikCustomInput';
import Radios from "./components/radio";

export interface IFormikRadiosInputProps {
  name: string;
  labelRight?: string;
  labelLeft?: string;
  form?: any;
  field?: any;
  okMode?: boolean;
  flip?: boolean;
  labelInMiddleMode?: boolean;
}


const FormikRadiosInput = ({...props}: IFormikRadiosInputProps) => (
  <FormikCustomInput
    {...props}
    hideLabel={true}
    inputComponent={inputProps => <Radios
      {...inputProps}
      okMode={inputProps.okMode}
      setState={(state) => inputProps.form.setFieldValue(inputProps.field.name, inputProps.flip ? !state : state)}
      state={inputProps.flip ? !inputProps.field.value : inputProps.field.value}
      labelInMiddleMode={props.labelInMiddleMode}
    />}
  />
);

FormikRadiosInput.defaultProps = {
  name: "",
  label: "",
  okMode: false,
  labelInMiddleMode: false,
};

export default FormikRadiosInput;
