import React from 'react';
import {Helmet} from "react-helmet";
import {config} from 'data/utils/constants';

interface ISeoProps {
  title: string;
  metaDescription: string;
  pathName: string;
  loading?: boolean;
}

const Seo = ({loading, title, metaDescription, pathName}: ISeoProps) => loading ? null : (
  <Helmet>
    <title>{title || ''}</title>
    <meta name="description" content={metaDescription || ''}/>
    <link rel="canonical" href={pathName ? `${config.baseURL}/${pathName}` : window.location.href}/>
  </Helmet>
);
Seo.defaultProps = {};
export default React.memo(Seo);
