import React from 'react';
import styles from './InnerCard.module.scss';
import SkeletonPlaceHolder, {eSkeletonVariant} from 'gui/components/skeletonPlaceHolder';

export interface IBehindCardProps {
  title?: string;
  content?: string[] | string | any[] | any;
  children?: any;
  loading?: boolean;
}

const contentToRows = (contentLines, loading = false) => (!Array.isArray(contentLines) ? [contentLines] : contentLines)
  .filter(content => content)
  .map(content => typeof content === 'string' ? content.trim() : content)
  .map((content, index) => loading ? <SkeletonPlaceHolder key={index}  variant={eSkeletonVariant.text}/> :
    <li key={index}>{content}</li>);

const InnerCard = (props: IBehindCardProps) => {
  return (<div className={styles.InnerCard}>
      <h3 className={styles.title}>
        {props.loading ?
          <SkeletonPlaceHolder width={175} className={styles.middle} variant={eSkeletonVariant.text}/> : props.title}
      </h3>
      <ol className={props.loading ? styles.middle : styles.content}>
        {contentToRows(props.content, props.loading)}
      </ol>
      {props.children}
    </div>
  );
};

InnerCard.defaultProps = {
  title: '',
  children: null,
  loading: false,
};

export default React.memo(InnerCard);
