import React, {useEffect} from 'react';
import ScrollToTop from 'react-scroll-to-top';

interface IScrollToTopExtended {
  top?: number;
  smooth?: boolean;
  svgPath?: string;
  viewBox?: string;
  component?: any;
  scrollOnRender?: any;
  scollOnChange?: any;
  noButton?: boolean;

  bottom?: number;
}

const ScrollToTopExtended = ({noButton, scrollOnRender, scollOnChange, bottom, ...props}: IScrollToTopExtended) => {
  useEffect(() => {
    if (scrollOnRender !== undefined) {
      if (typeof scrollOnRender === 'string' && scrollOnRender.length > 0 && scrollOnRender.startsWith("#")) {
        const performScrollToElement = () => {
          const yourElement = document.getElementById(scrollOnRender.slice(1));
          const y = yourElement.getBoundingClientRect().top + window.pageYOffset;
          window.scrollTo({top: y, behavior: 'smooth'});
        }
        const pageOffset = window.pageYOffset;
        performScrollToElement();
        setTimeout(() => {
          /**
           * If page offset changed, it means some elements were added/removed dynamically,  just scroll again
           */
          if (pageOffset !== window.pageYOffset) {
            performScrollToElement();
          }
        }, 1000);
      } else {
        window.scroll({top: 0, behavior: "smooth"});
      }
    }
  }, [scrollOnRender, scollOnChange]);
  return noButton ? null : (
    <ScrollToTop style={{bottom: bottom, zIndex: 999999}} {...props}/>
  );
};

ScrollToTopExtended.defaultProps = {
  label: 'test',
  value: 0,
  noButton: true,
  bottom: 40,
};

export default React.memo(ScrollToTopExtended);
