import React from 'react';
import moment from 'moment';
import {When} from "react-if";
import {generateSluggedAdFullUrl} from 'data/redux/actions/guiActions';
import {currencyValueToISO} from 'gui/components/formikCurrencySelector';
import {
  productCompanyNameSelector,
  productEndingSelector,
  productHotelSelector
} from 'data/redux/selectors/productSelector';
import {IProduct} from 'data/interfaces/products';

interface IAdRichDataProps {
  product: IProduct;
}

const AdRichData = ({product}: IAdRichDataProps) => (
  <>
    <link itemProp="url" href={generateSluggedAdFullUrl(product)}/>
    <meta itemProp="acceptedPaymentMethod" content="http://purl.org/goodrelations/v1#MasterCard"/>
    <meta itemProp="acceptedPaymentMethod" content="http://purl.org/goodrelations/v1#VISA"/>
    <meta itemProp="acceptedPaymentMethod" content="http://purl.org/goodrelations/v1#PayPal"/>
    <meta itemProp="availability" content="https://schema.org/InStock"/>
    <meta itemProp="availabilityStarts" content={moment(new Date(product.createdAt)).format("YYY-MM-DD")}/>

    <meta itemProp="itemOffered" content='Trip'/>


    <meta itemProp="priceCurrency" content={currencyValueToISO[product.saleDetails.currency]}/>
    <meta itemProp="itemCondition" content="https://schema.org/UsedCondition"/>
    <meta itemProp="price" content={`${product.saleDetails.askingPrice}`}/>
    <meta itemProp="priceValidUntil"
          content={moment(new Date(productEndingSelector(product))).format("YYY-MM-DD")}/>
    <When condition={!!(productHotelSelector(product))}>
      {() =>
        <>
          <div style={{display: 'none'}} itemProp="aggregateRating" itemType="http://schema.org/AggregateRating"
               itemScope>
            <meta itemProp="reviewCount" content={`${(productHotelSelector(product).reviews || []).length}`}/>
            <meta itemProp="ratingValue" content={`${productHotelSelector(product).rating}`}/>
          </div>
        </>
      }
    </When>

    <div style={{display: 'none'}} itemProp="brand" itemType="http://schema.org/Brand" itemScope>
      <meta itemProp="name" content={productCompanyNameSelector(product)}/>
    </div>

  </>
);

AdRichData.defaultProps = {
};
export default AdRichData;
