import React, {useEffect} from "react";
import {FastField} from 'formik';
import {TextField} from 'formik-material-ui';
import styles from './FormikTextInput.module.scss';
import {When} from "react-if";
import {FormHelperText} from '@material-ui/core';
import SkeletonPlaceHolder from 'gui/components/skeletonPlaceHolder';

export interface ITextInputProps {
  label?: string;
  name: string;
  id?: string;
  autoComplete?: string;
  values: any;
  errors: any;
  touched: any;
  onChange?: any;
  setFieldError?: any;
  customErrorsObject?: any;
  loading?: boolean;
}


export const CustomErrorsRenderer = ({customErrorsObject}: any) => (<When
  condition={!!(customErrorsObject && typeof customErrorsObject === 'object' && Object.keys(customErrorsObject).length > 0)}>{() =>
  Object.keys(customErrorsObject)
    .filter(key => customErrorsObject[key])
    .map(key => <FormHelperText key={key} error>{customErrorsObject[key]}</FormHelperText>)
}
</When>)

const FormikTextInput = ({
                           name,
                           id,
                           label,
                           autoComplete,
                           errors,
                           touched,
                           values,
                           customErrorsObject,
                           setFieldError,
                           loading,
                           ...rest
                         }: ITextInputProps) => {
    useEffect(() => {
      if (setFieldError && !!(customErrorsObject && typeof customErrorsObject === 'object' && Object.keys(customErrorsObject).length > 0)) {
        Object.keys(customErrorsObject)
          .filter(key => customErrorsObject[key])
          .filter(key => !(errors[name || id] || []).includes(customErrorsObject[key]))
          .forEach(key => {
            setFieldError(name || id, customErrorsObject[key]);
          });
      }
    }, [customErrorsObject, errors, touched, name, id, setFieldError]);
    return (<>
        {loading ?
          <SkeletonPlaceHolder
            className={styles.loadingField}
          >
            {label}
          </SkeletonPlaceHolder>
          :
          <FastField
            component={TextField}
            label={label}
            name={name || id}
            id={id || name}
            variant="outlined"
            autoComplete={autoComplete}
            className={`${styles.TextField} ${(!errors[name || id] && (touched[name || id] || values[name || id])) ? styles.valid : ""}`.trim()}
            required
            fullWidth
            {...rest}
          />
        }
      </>
    )
  }
;

FormikTextInput.defaultProps = {
  label: "",
  values: [],
  errors: [],
  touched: [],
  loading: false,
};

export default FormikTextInput;
