import React from "react";
import styles from './NewsLettersCheckbox.module.scss';
import {FastField} from 'formik';
import {CheckboxWithLabel} from 'formik-material-ui';
import SkeletonPlaceHolder from 'gui/components/skeletonPlaceHolder';

interface ITermsOfServiceCheckboxProps {
  values: any;
  errors: any;
  touched: any;
  setFieldValue: any;
  loading?: boolean;
}

const NewsLettersCheckbox = ({loading, ...props}: ITermsOfServiceCheckboxProps) =>
  loading ?
    (<SkeletonPlaceHolder
      variant="text"
      width={300}
    />) :
    (<FastField
      type="checkbox"
      component={CheckboxWithLabel}
      Label={{label: 'אני מעוניין/ת לקבל הודעות שיווקיות מדיל אנד גו'}}
      name="allowAdvertisementEmails"
      value="allowAdvertisementEmails"
      color="primary"
      className={[styles.acceptedNewsletters, (!props.errors.allowAdvertisementEmails && props.touched.allowAdvertisementEmails) ? "valid" : ""].join(" ")}
      checked={props.values.allowAdvertisementEmails}
      onClick={() => {
        props.setFieldValue("allowAdvertisementEmails", !props.values.allowAdvertisementEmails);
      }}
    />);

NewsLettersCheckbox.defaultProps = {
  values: {},
  errors: {},
  touched: {},
  submitCount: 0,
  setFieldValue: () => {
  },
  loading: false,
};

export default React.memo(NewsLettersCheckbox);
