import React from 'react';
import {FormHelperText, Grid} from '@material-ui/core';
import LinkBlockContainer from 'data/redux/containers/linkBlockContainer/LinkBlockContainer';
import {Form, Formik} from 'formik';
import * as Yup from "yup";
import styles from './SignInForm.module.scss';
import FormikPasswordInput from 'gui/components/formikPasswordInput/FormikPasswordInput';
import {linksMap} from 'data/utils/constants';
import FormikErrorScrollToTop from 'formik-top-error-focus';
import FormikTextInput from 'gui/components/formikTextInput';
import Recaptcha from 'gui/components/recaptcha/Recaptcha';
import NextButton from 'gui/components/nextButton/NextButton';


interface ISignUpFormProps {
  onSubmit: (formValues) => Promise<any>;
  invalidLogin?: boolean;
  loading?: boolean;
}

const SignInForm = ({loading, ...props}: ISignUpFormProps) => {
  return (
    <Formik
      initialValues={{
        email: "",
        password: "",
        showPassword: false,
      }}
      validationSchema={Yup.object({
        email: Yup.string()
          .email("מייל לא תקין")
          .required("נדרש מייל"),
        password: Yup.string()
          .required('נדרשת סיסמה')
          .min(8, 'סיסמה קצרה מדיי, מינימום 8 אותיות או ספרות')
          .matches(/[a-zA-Z0-9]/, 'סיסמה יכולה רק להכיל אותיות באנגלית ומספרים'),

      })}
      onSubmit={async (values, {setSubmitting}) => {
        setSubmitting(false);
        props
          .onSubmit(values)
          .then(() => {
          });
      }}
      enableReinitialize={true}
    >
      {({submitForm, isSubmitting, touched, errors, values}) => (
        <Form translate={null} className={styles.signInForm}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              {props.invalidLogin ? <FormHelperText error>פרטי כניסה לא נכונים</FormHelperText> : null}
            </Grid>
            <Grid item xs={12} style={{marginTop: 10}} sm={12}>
              <FormikTextInput
                label="מייל"
                name="email"
                autoComplete="email"
                values={values}
                errors={errors}
                touched={touched}
                loading={loading}
              />
            </Grid>
            <Grid item xs={12} style={{marginTop: 15}} sm={12}>
              <FormikPasswordInput
                label="סיסמה"
                name="password"
                loading={loading}
              />
            </Grid>
            <Grid item xs={6}/>
            <Grid item style={{marginTop: -5}} xs={6}>
              <LinkBlockContainer style={{color: '#05BEC0', marginTop: 0, padding: 0, fontSize: 14}}
                                  className="text-left text-capitalize" to={linksMap.forgotPassword}>
                שכחת סיסמה?
              </LinkBlockContainer>
            </Grid>

            <Grid item xs={12}>
              <Recaptcha loading={loading}/>
            </Grid>

            <Grid item xs={1}/>
            <Grid item style={{marginTop: 15}} xs={10}>
              <NextButton
                isDisabled={isSubmitting}
                onClick={submitForm}
                loading={loading}
              >
                התחברות
              </NextButton>
            </Grid>
            <Grid item xs={1}/>
          </Grid>
          <Grid container justify="flex-end">
            <Grid item>
            </Grid>
          </Grid>
          <FormikErrorScrollToTop/>
        </Form>
      )}
    </Formik>
  );
};

SignInForm.defaultProps = {
  onSubmit: (formValues) => new Promise((resolve) => {
    
    setTimeout(resolve, 1000);
  }),
  invalidLogin: false,

  loading: false,
};

export default React.memo(SignInForm);
