import React from 'react';
import styles from './ViewAdButton.module.scss';
import {IProduct} from 'data/interfaces/products';
import LinkBlock from 'gui/components/linkBlock/LinkBlock';
import VisibilityIcon from '@material-ui/icons/Visibility';
import {generateSluggedAdUrl} from 'data/redux/actions/guiActions';
import {Tooltip} from '@material-ui/core';
import CircleLoader from '../circleLoader';

interface IAdProps {
  product: IProduct;
  className?: string;
  loading?: boolean;
}


const ViewAdButton = ({loading, ...props}: IAdProps) => loading ? <CircleLoader/> : (
  <LinkBlock to={generateSluggedAdUrl(props.product)}
             className={`${styles.controlButton} ${props.className}`}>
    <Tooltip title="צפיה" aria-label="view" arrow enterDelay={0}>
      <VisibilityIcon/>
    </Tooltip>
  </LinkBlock>
);

ViewAdButton.defaultProps = {
  product: {},
  className: ''
};
export default ViewAdButton;
