import React from 'react';
import styles from './HotelReviewText.module.scss';

import ShowMoreText from 'gui/components/showMoreText/ShowMoreText';
import {IHotelDetailsWithTerms} from 'gui/components/editors/hotelEditor/components/hotelDetailsForm';


interface IHotelReview {
  review: IHotelDetailsWithTerms["hotel"]['reviews'][0]
}


const HotelReviewText = (props: IHotelReview) => <ShowMoreText
  className={styles.content}
>
  {props.review.text}
</ShowMoreText>;

HotelReviewText.defaultProps = {};

export default React.memo(HotelReviewText);
