import React from "react";
import styles from './StatusSelector.module.scss';
import FormikSearchSelector from "gui/components/formikSearchSelector/FormikSearchSelector";
import {allProductStates} from 'data/redux/constants/productsConstants';
import {statusToText} from 'gui/components/listPage/components/ad/components/adStatus';
import {Grid, Typography} from '@material-ui/core';
import {components} from 'react-select';
import LinkBlock from 'gui/components/linkBlock';
import SearchRoundedIcon from '@material-ui/icons/SearchRounded';
import {When} from 'react-if';

export interface IStatusSelectorProps {
  label: string;
  statuses: string[];
  statusCount?: any;
  componentMode?: boolean;
  listPathName?: string;

  selectOnlyMode?: boolean;
}

const Line = props => <Grid container>
  <Grid item xs={12}>
    <Typography className={styles.optionText}>
      {props.data.label}
      <When condition={props.data.count > 0}>
        {() => <>
          &nbsp;
          <span
            className={styles.optionCount}>({props.data.count})
    </span>
        </>}
      </When>
    </Typography>
  </Grid>
</Grid>

const Option = ({children, ...restProps}) => {
  if (restProps.selectProps.componentMode === false && restProps.selectProps.listPathName) {
    return (
      <components.Option {...restProps}>
        <LinkBlock to={`${restProps.selectProps.listPathName}/${restProps.data.value}`}>
          <Line {...restProps}/>
        </LinkBlock>
      </components.Option>
    );
  } else {
    return (
      <components.Option {...restProps}>
        <Line {...restProps}/>
      </components.Option>
    );
  }
};

const SingleValue = props => {
  return (
    <components.SingleValue {...props}>
      <Line {...props}/>
    </components.SingleValue>
  )
};

const ALL_ADS = 0;
/**
 * combine all counts into one
 * @param props
 */
const setAllCount = (props) => {
  let cnt = 0;
  if (props.statusCount && typeof props.statusCount === 'object') {
    const statuses = props.statuses.map(({value}) => value);
    const keys = Object.keys(props.statusCount)
      .map(value => parseInt(value))
      .filter(key => statuses.includes(key) && key !== ALL_ADS);
    for (const key of keys) {
      cnt += props.statusCount[key];
    }
    props.statusCount[ALL_ADS] = cnt;
  }
  return cnt;
};

const DropdownIndicator = props => {
  return (<SearchRoundedIcon className={styles.searchIcon}/>)
}


const StatusSelector = (props: IStatusSelectorProps) => {
  setAllCount(props);
  return (
    <FormikSearchSelector
      className={styles.statusSelector}
      options={props
        .statuses
        .filter(({label}: any) => {
          if (props.selectOnlyMode && label === allLabel)
            return false
          return label;
        })
        .map(({value, ...rest}: any) => ({
          ...rest,
          value,
          count: props.selectOnlyMode ? -1 : (props.statusCount[value] || 0),
        }))
        .filter(({count}) => props.selectOnlyMode ? true : count > 0)
      }
      components={{Option, SingleValue, DropdownIndicator}}
      {...props}
      isClearable={false}
    />
  );
};

const defaultStatuses = allProductStates
  .map((state) => ({
    label: statusToText(state), value: state,
  }));

const allLabel = 'כל המודעות';

defaultStatuses.unshift({
  label: allLabel,
  value: ALL_ADS,
});

StatusSelector.defaultProps = {
  statuses: defaultStatuses,
  statusCount: {},
  selectOnlyMode: false,
};

export default StatusSelector;
