export const syncDates = async (props, name, value, directions = {forward: true, backward: false}) => {
  const timeOrder = [
    'originTimeOfTakeOff',
    'originTimeOfLanding',
    'checkInDate',
    'checkOutDate',
    'destinationTimeOfTakeOff',
    'destinationTimeOfLanding',
  ]
    .filter(timeOrder => timeOrder in props.values);
  const getPrevious = currentName => timeOrder[timeOrder.indexOf(currentName) - 1];
  const getNext = currentName => timeOrder[timeOrder.indexOf(currentName) + 1];
  const getAll = (currentName, direction: (currentName: string) => string) => {
    const order = [];
    let nextName = direction(currentName);
    if (nextName) {
      order.push(nextName);
      while (nextName = direction(nextName))
        order.push(nextName);
    }
    return order;
  }
  const setDateTime = (name, value) => props.setFieldValue(name, value, false);
  if (typeof value !== 'number') {
    value = value.getTime();
  }

//  await setDateTime(name, value);

  const minute = 1000 * 60;
  if (directions.backward) {
    for (const timeName of getAll(name, getPrevious)) {
      const getCurrentTime = () => props.values[timeName];
      if (!getCurrentTime() || getCurrentTime() > value) {
        await setDateTime(timeName, value);
      }
    }
  }
  if (directions.forward) {
    for (const timeName of getAll(name, getNext)) {
      const getCurrentTime = () => props.values[timeName];
      if (!getCurrentTime() || getCurrentTime() < value) {
        await setDateTime(timeName, value);
      }
    }
  }
};
