import React from 'react';
import {If, Then} from "react-if";
import styles from './AdStreet.module.scss';

import LocationOnRoundedIcon from '@material-ui/icons/LocationOnRounded';
import GenericDetail
  from 'gui/components/listPage/components/ad/components/genericDetail';
import {IHotelDetailsWithTerms} from 'gui/components/editors/hotelEditor/components/hotelDetailsForm';

interface IAdStreetProps {
  hotelDetails: IHotelDetailsWithTerms;

  className?: string;
}

/**
 <LinkBlock
 href={`https://maps.google.com/?q=${encodeURIComponent(props.hotelDetails.hotel.formatted_address)}`}
 style={{
                  margin: 0,
                  padding: 0,
                  display: 'inline'
                }}>{props.hotelDetails.hotel.vicinity}</LinkBlock>
 */
const AdStreet = (props: IAdStreetProps) => (
  <If
    condition={!!(props.hotelDetails.hotel)}>
    <Then>
      {() => (
        <GenericDetail
          className={props.className}
          title="כתובת"
          icon={LocationOnRoundedIcon}
          enableReadMore={true}
        >
          <span className={styles.address}>
              {props.hotelDetails.hotel.vicinity}
          </span>
        </GenericDetail>
      )}
    </Then>
  </If>
);

AdStreet.defaultProps = {
  hotelDetails: {},
};
export default AdStreet;
