import React from 'react';

import LanguageRoundedIcon from '@material-ui/icons/LanguageRounded';
import GenericDetail
  from 'gui/components/listPage/components/ad/components/genericDetail';
import LinkBlock from 'gui/components/linkBlock';

import {When} from 'react-if';
import {IHotelDetailsWithTerms} from 'gui/components/editors/hotelEditor/components/hotelDetailsForm';
import {shortUrl} from 'data/utils/shortcuts';

interface IAdHotelWebsiteProps {
  hotelDetails: IHotelDetailsWithTerms
}

const AdHotelWebsite = (props: IAdHotelWebsiteProps) => (
  <When condition={!!props.hotelDetails.hotel.website}>
    {() => <GenericDetail
      title="קישור למלון"
      icon={LanguageRoundedIcon}
    >
      <LinkBlock href={props.hotelDetails.hotel.website}
                 style={{
                   margin: 0,
                   padding: 0,
                   display: 'inline'
                 }}>{shortUrl(props.hotelDetails.hotel.website)}</LinkBlock>
    </GenericDetail>}
  </When>
);

AdHotelWebsite.defaultProps = {
  hotelDetails: {},
};
export default AdHotelWebsite;
