import React from "react";
import styles from "./SellerComments.module.scss";
import InfoCard from 'gui/pages/mainRouter/pages/ad/components/commons/infoCard';
import ErrorRoundedIcon from '@material-ui/icons/ErrorRounded';
import Comments from './components/comments';
import Detail, {IDetailProps} from './components/detail';

export interface ISellerCommentsProps {
  title?: string;
  description?: string;
  icon?: any;
  iconContainerClassName?: string;
  details?: IDetailProps[];
}


const SellerComments = ({title, description, details, icon, iconContainerClassName}: ISellerCommentsProps) => {
  return (
    <InfoCard
      title={title}
      icon={icon}
      toolTip={title}
      iconContainerClassName={iconContainerClassName}
    >
      <div className={styles.SellerComments}>
        <Comments description={description}/>
        {(details || [])
          .map((detail, index) => <Detail key={index} {...detail}/>)
        }
      </div>
    </InfoCard>
  );
}

SellerComments.defaultProps = {
  title: "",
  description: "",
  icon: ErrorRoundedIcon,
  details: [],
};

export default SellerComments;
