import React, {useState} from 'react';
import styles from './HotelReviewsCard.module.scss';
import {IProduct} from 'data/interfaces/products';
import InfoCard from 'gui/pages/mainRouter/pages/ad/components/commons/infoCard';
import {MDBCol, MDBContainer, MDBRow} from 'mdbreact';
import ChatBubbleRoundedIcon from '@material-ui/icons/ChatBubbleRounded';
import HotelReview
  from 'gui/pages/mainRouter/pages/ad/components/adHotel/components/firstStage/components/hotelReviewsCard/components/hotelReview';
import LinkBlock from 'gui/components/linkBlock';
import {randomArrayValue} from 'data/utils/calculators';
import fill from 'fill-range';
import {Else, If, Then, Unless, When} from 'react-if';

interface IHotelReviewsCardProps {
  product?: IProduct;
  initialReviewsShown?: number;
  disableLoadMore?: boolean;
}

const HotelReviewsCard = (props: IHotelReviewsCardProps) => {
  const [showMoreCount, addShowMore] = useState(props.initialReviewsShown);
  return (
    <When
      condition={!!(props.product.hotelDetails.hotel.reviews && props.product.hotelDetails.hotel.reviews.length)}>{
      () => <InfoCard
        className={styles.hotelsReviewsCard}
        title={(<>
          <span className={styles.hotelReviewsTitleRight}>ביקורת</span>
          <span className="">
          (מאת <LinkBlock style={{display: 'inline', padding: 0, margin: 0}}
                          href={randomArrayValue(...props.product.hotelDetails.hotel.reviews).author_url}>&nbsp;Google</LinkBlock>):
          </span>
        </>)}
        icon={ChatBubbleRoundedIcon}
        toolTip="מלון"
      >
        <MDBContainer fluid>
          {fill(0, Math.min(showMoreCount, props.product.hotelDetails.hotel.reviews.length / 2)).map(index => (
            <MDBRow key={index} style={{marginBottom: 15}} className="animated flipInX">
              {fill(0, 1).map(i => <MDBCol key={i} sm="6">
                <If condition={props.product.hotelDetails.hotel.reviews.length > i + (2 * index)}>
                  <Then>{() => <HotelReview
                    review={props.product.hotelDetails.hotel.reviews[i + (2 * index)]}/>}</Then>
                </If>
              </MDBCol>)}
            </MDBRow>
          ))}
          <Unless condition={props.disableLoadMore}>{() =>
            <MDBRow>
              <MDBCol sm="12">
                <If condition={props.product.hotelDetails.hotel.reviews.length / 2 > showMoreCount + 1}>
                  <Then>
                    <div className={styles.showMoreButton} onClick={() => addShowMore(showMoreCount + 1)}>טען ביקורות
                      נוספות
                    </div>
                  </Then>
                  <Else>
                    <LinkBlock href={props.product.hotelDetails.hotel.url} style={{padding: 0, margin: 0}}>
                      <div className={styles.showMoreButton}>טען ביקורות נוספות</div>
                    </LinkBlock>
                  </Else>
                </If>
              </MDBCol>
            </MDBRow>
          }</Unless>
        </MDBContainer>
      </InfoCard>
    }
    </When>
  )
};

HotelReviewsCard.defaultProps = {
  initialReviewsShown: 0,
  disableLoadMore: false,
};

export default React.memo(HotelReviewsCard);
