import React from "react";
import styles from './FormikDateSelectorTimeSelector.module.scss';
import {DateTimePicker, MuiPickersUtilsProvider} from '@material-ui/pickers';
import MomentUtils from '@date-io/moment';
import FormErrorDisplay from '../formErrorDisplay';
import {getDatePlusDays} from 'data/utils/calculators';

export interface IOriginSelectorProps {
  label: string;
  options: string[];
  rtl: boolean;
  field?: any;
  form?: any;
  handleChange?: any;
  noBorder?: boolean;
  minDate?: Date;
  maxDate?: Date;
  openTo?: string;
}

const FormikDateTimeSelector = (props: IOriginSelectorProps) => {
  const touched = props.form.touched[props.field.name];
  const error = touched && props.form.errors[props.field.name];
  const valid = touched && !error;
  return (
    <>
      <MuiPickersUtilsProvider utils={MomentUtils} locale={"en"}>
        <DateTimePicker
          id={props.field.name}
          name={props.field.name}
          autoOk
          ampm={false}
          fullWidth={true}
          variant="inline"
          inputVariant="outlined"
          label={props.label}
          value={props.field.value && new Date(props.field.value)}
          format="MMM D YYYY, HH:mm"
          onChange={(value) => {
            props.field.onChange({
              target: {
                name: props.field.name,
                value: value.toDate().getTime(),
              },
            });
            if (props.handleChange)
              props.handleChange(value.toDate().getTime());
          }}
          onBlur={props.field.onBlur}
          className={`${styles.TextField} ${(error ? `${styles.error} ` : "")} ${(valid ? `${styles.valid}` : "")} ${props.noBorder ? styles.noBorder : ''}`.trim()}
          minDate={props.minDate}
          maxDate={props.maxDate}
          invalidLabel={null}
          invalidDateMessage={null}
          openTo={props.openTo as any}
        />
      </MuiPickersUtilsProvider>
      <FormErrorDisplay
        error={error}
      />
    </>
  );
};

FormikDateTimeSelector.defaultProps = {
  options: [],
  rtl: true,
  minDate: getDatePlusDays(1),
  maxDate: getDatePlusDays(365 * 100, new Date()),
  openTo: 'date',
};

export default FormikDateTimeSelector;
