import React from "react";
import styles from './TransferableCheckbox.module.scss';
import {FastField} from 'formik';
import {CheckboxWithLabel} from 'formik-material-ui';
import {FormHelperText} from '@material-ui/core';

interface ITermsOfServiceCheckboxProps {
  values: any;
  errors: any;
  touched: any;
  submitCount: number;
  setFieldValue: any;
}

const TransferableCheckbox = (props: ITermsOfServiceCheckboxProps) => {
  return (
    <>
      <FastField
        type="checkbox"
        component={CheckboxWithLabel}
        Label={{
          label:
            <span>
              אני מאשר כי השובר הנמצא ברשותי ניתן להעברה
            </span>
        }}
        name="acceptedTransferable"
        value="acceptedTransferable"
        color="primary"
        className={[styles.acceptedTerms, ((!props.errors.acceptedTransferable && props.touched.acceptedTransferable) ? "valid" : "")].join(" ")}
        checked={props.values.acceptedTransferable}
        onClick={() => props.setFieldValue("acceptedTransferable", !props.values.acceptedTransferable)}/>
      {(props.errors.acceptedTransferable && props.submitCount > 0) ?
        <FormHelperText style={{marginTop: -15, position: "absolute"}}
                        error>{props.errors.acceptedTransferable}</FormHelperText> : null}
    </>
  );
};

TransferableCheckbox.defaultProps = {
  values: {},
  errors: {},
  touched: {},
  submitCount: 0,
  setFieldValue: () => {
  },
};

export default React.memo(TransferableCheckbox);
