import React from 'react';
import styles from './TimeAndStatusConclusion.module.scss';
import {IProduct} from 'data/interfaces/products';
import {IOrder} from 'data/interfaces/orders';
import {MDBCol, MDBRow} from 'mdbreact';
import {productEndingSelector} from 'data/redux/selectors/productSelector';
import {getHours} from 'data/utils/calculators';
import {Else, If, Then, When} from 'react-if';
import AdStatus from 'gui/components/listPage/components/ad/components/adStatus';
import {statusAppendTemplate} from 'gui/pages/controlRouter/pages/ordersByMe/components/orderAd/OrderAd';

interface IPurchaseFormProps {
  product?: IProduct;
  order?: IOrder;
  isProductOwner?: boolean;
  isOrderOwner?: boolean;
}

const TimeAndStatusConclusion = (props: IPurchaseFormProps) =>
  <div className={styles.saleConclusionBottom}>
    <When condition={props.isProductOwner}>
      <When condition={new Date().getTime() < productEndingSelector(props.product)}>
        <MDBRow style={{marginTop: 10, fontSize: "1em"}}>
          <MDBCol className={styles.label} sm="6">
            זמן שנותר:
          </MDBCol>
          <MDBCol className={`${styles.value} disableWordBreak`}
                  sm="6">
            {getHours(new Date().getTime(), productEndingSelector(props.product))}
          </MDBCol>
        </MDBRow>
      </When>
    </When>
    <When condition={!props.isProductOwner}>
      <MDBRow>
        <MDBCol className={`${styles.value} disableWordBreak`}
                sm="12">
          <If condition={props.isOrderOwner}><Then>
            <AdStatus order={props.order} statusToText={statusAppendTemplate}/>
          </Then>
            <Else>
              <AdStatus product={props.product}/>
            </Else>
          </If>
        </MDBCol>
      </MDBRow>
    </When>
  </div>;

TimeAndStatusConclusion.defaultProps =
  {
    product: {}
    ,
    order: {}
    ,
  };

export default React.memo(TimeAndStatusConclusion);
