import React from 'react';
import styles from './HotelDetailsCard.module.scss';
import {IProduct} from 'data/interfaces/products';
import HotelIcon from '@material-ui/icons/Hotel';
import HotelImageSquare from './components/hotelImagesSquare';
import InfoCard from 'gui/pages/mainRouter/pages/ad/components/commons/infoCard';
import HotelInfo from './components/hotelInfo';
import {formatTitle} from 'data/utils/shortcuts';
import {SizeMe} from 'react-sizeme';

interface IFirstPageProps {
  product?: IProduct;
}

const HotelDetailsCard = (props: IFirstPageProps) => (
  <SizeMe>{({size: {width}}) => {
    const isMobile = width <= 546;
    if (isMobile) {
      return <div>
        <HotelImageSquare product={props.product}/>
        <InfoCard
          title={"פרטי מלון"}
          icon={HotelIcon}
          toolTip="מלון"
          style={{marginTop: 14}}
          defaultExpanded={true}
        >
          <HotelInfo product={props.product} hideTitle/>
        </InfoCard>
      </div>
    } else
      return (
        <InfoCard
          title={formatTitle(props.product.hotelDetails.hotel.name)}
          icon={HotelIcon}
          toolTip="מלון"
          defaultExpanded={true}
        >
          <HotelImageSquare product={props.product}>
            <HotelInfo product={props.product} className={styles.HotelInfo}/>
          </HotelImageSquare>
        </InfoCard>);
  }
  }</SizeMe>
);


HotelDetailsCard.defaultProps = {};

export default React.memo(HotelDetailsCard);
