import React from "react";
import styles from './Radio.module.scss';
import RadioCircle from './components/radioCircleSvg';
import {Else, If, Then, When} from 'react-if';
import OkRadioCircle from './components/okRadioCircle';
import NotOkRadioCircle from './components/notOkRadioCircle';


export interface IRadioProps {
  setState?: (boolean: boolean) => void;
  label?: string;
  okMode?: boolean;
  state?: boolean;
  labelInMiddleMode?: boolean;
}


const Radio = (props: IRadioProps) => {
  return (
    <span className={`${styles.Radio} ${props.labelInMiddleMode ? styles.labelInMiddleMode : ''}`.trim()}
          onClick={() => props.setState(!props.state)}>
      <If condition={!!props.okMode}>
        <Then>
          {() => <>
            <NotOkRadioCircle
              state={!props.state}
            />
            <When condition={props.labelInMiddleMode}>
              {() => props.label}
            </When>
            <OkRadioCircle
              state={props.state}
            />
          </>}
        </Then>
        <Else>
               {() => <>
                 <RadioCircle
                   state={props.state}
                 />
                 <When condition={props.labelInMiddleMode}>
                   {() => props.label}
                 </When>
                 <RadioCircle
                   state={!props.state}
                 />
               </>}
        </Else>
      </If>

    </span>
  );
};

Radio.defaultProps = {
  okMode: false,
  labelInMiddleMode: false,
};

export default Radio;
