import React, {useState} from 'react';
import styles from "./CustomTextField.module.scss";
import {InputAdornment, TextField as NoFormikTextField} from '@material-ui/core';
import SkeletonPlaceHolder from 'gui/components/skeletonPlaceHolder';
import {ReactComponent as CrossIcon} from 'assets/img/crossIcon.svg';
import {promiseWait} from 'data/utils/shortcuts';

interface ICustomTextFieldProps {
  label: string;
  value: string | number;
  disabled: boolean;
  readOnly?: boolean;
  hideWhenNoValue?: boolean;
  noBorder?: boolean;
  className?: string;
  customErrorsObject?: any;
  loading?: boolean;

  name?: any;
  onChange?: any;

  InputProps?: any;
}

const putElementIntoElement = (element, parentSelector, intoElementSelector) => {
  return promiseWait(500)
    .then(() => {
      try {
        if (element) {
          const parent = element.closest(parentSelector).querySelector(intoElementSelector);
          if (parent && typeof parent.append === 'function') {
            parent.append(element);
            return true;
          }
        }
      } catch (e) {
      }
      return false;
    })
};

const CustomTextField = ({loading, hideWhenNoValue, noBorder, ...props}: ICustomTextFieldProps) => {
    const [textInput, setTextInput] = useState(null);
    if (hideWhenNoValue && !props.value) {
      return null;
    }
    return (
      loading ?
        <SkeletonPlaceHolder
          className={styles.loadingField}
        >
          {props.label}
        </SkeletonPlaceHolder>
        :
        <>
          <NoFormikTextField
            inputRef={(input) => setTextInput(input)}
            variant="outlined"
            fullWidth
            readOnly
            inputProps={{
              readOnly: props.readOnly,
            }}
            {...props}
            className={`${styles.TextField} ${props.readOnly ? styles.readOnly : ''} ${noBorder ? styles.noBorder : ''} ${props.className}`.trim()}
            InputProps={
              {

                endAdornment: (
                  <InputAdornment position='end'>
                    <CrossIcon
                      className={`${styles.clearTextIcon} ${(props.readOnly || !props.value) ? styles.hidden : ''}`.trim()}
                      onClick={async (e) => {
                        if (props.onChange && props.name) {
                          const a = await props.onChange({
                            target: {
                              name: props.name,
                              value: '',
                            },
                          });
                          if (textInput) {
                            textInput.focus();
                          }
                        }
                      }}/>
                  </InputAdornment>
                ),
                ...props.InputProps,
              }}
          />
        </>
    );
  }
;
CustomTextField.defaultProps = {
  label: "",
  value: "",
  disabled: false,
  readOnly: false,
  hideWhenNoValue: false,
  noBorder: false,
  className: '',
  InputProps: {}
};
export default React.memo(CustomTextField);
