import React from "react";
import styles from "./Detail.module.scss";

export interface IDetailProps {
  title?: string;
  value?: string;
}


const Detail = ({title, value}: IDetailProps) => {
  return (
    <div className={styles.Detail}>
      <span>{title}</span>:
      &nbsp;
      <span>{value}</span>
    </div>
  )
}

Detail.defaultProps = {
  title: "",
  value: "",
};

export default Detail;
