import React from 'react';
import './EditProduct.css';
import {MDBCol, MDBContainer, MDBRow} from 'mdbreact';
import AuthenticatedPage from 'gui/components/authenticatedPage/AuthenticatedPage';
import {IProduct} from 'data/interfaces/products';
import {
  hotelDetails,
  identifyProductCategoryType,
  ticketDetails,
  vacationDetails,
  voucherDetails
} from 'data/redux/constants/productsConstants';
import FlightEditor from 'gui/components/editors/flightEditor';
import HotelEditor from 'gui/components/editors/hotelEditor';
import VacationEditor from 'gui/components/editors/vacationEditor';
import VoucherEditor from 'gui/components/editors/voucherEditor';
import CrispChatContainer from 'data/redux/containers/crispChatContainer';



const selectProductType = (props: ICreateProductProps) => {
  switch (identifyProductCategoryType(props.product)) {
    case ticketDetails:
      return (<FlightEditor {...props} />);
    case hotelDetails:
      return (<HotelEditor {...props} />);
    case vacationDetails:
      return (<VacationEditor {...props} />);
    case voucherDetails:
      return (<VoucherEditor {...props} />);
  }
  return (<div></div>);
}

interface ICreateProductProps {
  onSubmit: (product: IProduct) => Promise<any>;
  navigateOnLogout: () => void;
  product?: IProduct;
  id?: number;
  loadProductPage?: (id: number) => Promise<IProduct>;
  loading?: boolean;
}

class EditProduct extends AuthenticatedPage<ICreateProductProps> {
  componentDidMount() {
    super.componentDidMount();
    if (this.props.product.id !== this.props.id) {
      this.props.loadProductPage(this.props.id);
    }
  }

  render() {
    return (
      <MDBContainer fluid className="createProductContainer">
        <CrispChatContainer/>
        <MDBRow style={{margin: 0}}>
          <MDBCol xl="12">
            {selectProductType(this.props)}
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    );
  }
}


export default EditProduct;
