import React from 'react';
import styles from './AdReturnTime.module.scss';
import moment from 'moment';
import {Else, If, Then} from "react-if";

import GenericDetail
  from 'gui/components/listPage/components/ad/components/genericDetail';
import FlightLandRoundedIcon from '@material-ui/icons/FlightLandRounded';
import {ITicketDetailsWithTerms} from 'gui/components/editors/flightEditor/components/ticketDetailsForm';

interface IAdReturnTimeProps {
  ticketDetails: ITicketDetailsWithTerms;
}

const AdReturnTime = (props: IAdReturnTimeProps) => (
  <GenericDetail
    title="חזרה"
    icon={FlightLandRoundedIcon}
  >
    <If
      condition={!!(props.ticketDetails.destinationTimeOfTakeOff && props.ticketDetails.destinationTimeOfLanding)}>
      <Then>
        {() => (
          <>
              <span
                className={styles.time}>{moment(props.ticketDetails.destinationTimeOfTakeOff).format('D MMM YYYY, HH:mm')}</span>
            <span>&nbsp;</span>
            <span className={styles.city}>{props.ticketDetails.destinationAirport.city}</span>
          </>
        )}
      </Then>
      <Else>
        <span>
          אין
        </span>
      </Else>
    </If>
  </GenericDetail>
);

AdReturnTime.defaultProps = {
  ticketDetails: {},
};
export default AdReturnTime;
