import React from "react";
import styles from "./VoucherConditionComments.module.scss";
import {IProduct} from 'data/interfaces/products';
import SellerComments from 'gui/pages/mainRouter/pages/ad/components/commons/sellerComments';
import moment from 'moment';

export interface IVoucherConditionCommentsProps {
  product: IProduct;
}


const VoucherConditionComments = (props: IVoucherConditionCommentsProps) => <SellerComments
  title="מימוש השובר"
  description={props.product.voucherDetails.conditions}
  iconContainerClassName={styles.VoucherConditionComments}
  details={[
    {
      title: "בתוקף עד",
      value: moment(props.product.voucherDetails.expiration).format('MMM D YYYY'),
    }
  ]}
/>;

VoucherConditionComments.defaultProps = {
  product: {voucherDetails: {}},
};

export default VoucherConditionComments;
