import React from 'react';
import styles from './ChooseEditorTypeButtons.module.scss'
import ChooseEditorTypeButton, {IChooseEditorTypeButtonProps} from './components/chooseEditorTypeButton';


interface IChooseEditorTypeButtonsProps {
  links: IChooseEditorTypeButtonProps[];
}


const ChooseEditorTypeButtons = (props: IChooseEditorTypeButtonsProps) => (
  <div className={styles.ChooseEditorTypeButtons}>
    {props
      .links
      .map(link =>
        <ChooseEditorTypeButton key={link.link} {...link}/>
      )
    }
  </div>
);

ChooseEditorTypeButtons.default = {};
export default ChooseEditorTypeButtons;
