import React from "react";
import {IProduct} from 'data/interfaces/products';
import styles from "./HotelImageSquare.module.scss";
import HotelIcon from '@material-ui/icons/Hotel';
import {config} from "data/utils/constants";
import GenericImageSquare from "../genericImageSquare/GenericImageSquare";
import {ASSET} from 'data/redux/constants/assetsConstants';
import {IGuiReducerState} from 'data/redux/reducers/guiReducer';
import DesktopGallery from './components/desktopGallery';
import MobileGallery from './components/mobileGallery';


export interface IHotelImageSquare {
  product: IProduct;
  className: string;
  iconContainerClassName?: string;
  galleryOverrides?: any;
  label?: string;
  logoSrc?: string;
  logoTitle?: string;
  children?: any;
  gui?: IGuiReducerState;
}

const guiToBreakpoint = (props) => {
  try {
    if (props.gui && props.gui.breakpoints && props.gui.is) {
      for (const key in props.gui.is) {
        if (props.gui.is[key]) {
          return props.gui.breakpoints[key];
        }
      }
    }
  } catch (e) {
    console.error("Error gui to breakpoint", e);
  }
  return 480;
}

const guiIsMobile = (props) => {
  if (props.gui && props.gui.greaterThan) {
    return !props.gui.greaterThan.medium;
  }
  return false;
}

const generateAssetsList = props => props.product.assets
  .filter(({type}) => type === ASSET.PLACE)
  .map(({asset: {sourceLink}}) => ({
    original: `${config.baseURL}/placephoto/${sourceLink}?maxwidth=${props.gui, guiToBreakpoint(props) + 100}`,
    thumbnail: `${config.baseURL}/placephoto/${sourceLink}?maxwidth=${props.gui, guiToBreakpoint(props) + 100}`,
    originalAlt: props.product.hotelDetails.hotel.name,
    thumbnailAlt: props.product.hotelDetails.hotel.name,
  }));

const HotelImageSquare = (props: IHotelImageSquare) => (
  <GenericImageSquare
    className={`${styles.HotelImageSquare} HotelImageSquare ${props.className}`}
    iconContainerClassName={`${props.iconContainerClassName} ${styles.iconContainerClassName}`}
    icon={HotelIcon}
    label={props.label}
    logoSrc={props.logoSrc}
    logoTitle={props.logoTitle}
  >
    {guiIsMobile(props) ? <MobileGallery
        images={generateAssetsList(props)}
        galleryOverrides={props.galleryOverrides}
      >
        {props.children}
      </MobileGallery> :
      <DesktopGallery
        images={generateAssetsList(props)}
        galleryOverrides={props.galleryOverrides}
      >
        {props.children}
      </DesktopGallery>}


  </GenericImageSquare>
);

HotelImageSquare.defaultProps = {
  className: ``,
  label: 'מלון',

};

export default HotelImageSquare;
