import React from "react";
import styles from './TermsOfServiceCheckbox.module.scss';
import {FastField} from 'formik';
import {CheckboxWithLabel} from 'formik-material-ui';
import {FormHelperText} from '@material-ui/core';
import {When} from 'react-if';
import {linksMap} from 'data/utils/constants';
import SkeletonPlaceHolder from 'gui/components/skeletonPlaceHolder';

interface ITermsOfServiceCheckboxProps {
  values: any;
  errors: any;
  touched: any;
  submitCount: number;
  setFieldValue: any;
  loading?: boolean;
}

const TermsOfServiceCheckbox = ({loading, ...props}: ITermsOfServiceCheckboxProps) => loading ?
  (<SkeletonPlaceHolder
    variant="text"
    width={300}
  />) : (
    <>
      <FastField
        type="checkbox"
        component={CheckboxWithLabel}
        Label={{
          label:
            <span>
              קראתי ואני מאשר/ת תנאי השימוש&nbsp;
              <a
                className={styles.termsLink}
                target="_blank"
                href={linksMap.terms}>
                        וההגבלות
                      </a>
                    </span>
        }}
        name="acceptedTerms"
        value="acceptedTerms"
        color="primary"
        className={[styles.acceptedTerms, ((!props.errors.acceptedTerms && props.touched.acceptedTerms) ? "valid" : "")].join(" ")}
        checked={props.values.acceptedTerms}
        onClick={() => props.setFieldValue("acceptedTerms", !props.values.acceptedTerms)}/>
      <When condition={(!!props.errors.acceptedTerms && props.submitCount > 0)}>
        {() => <FormHelperText className={styles.helpError} style={{marginTop: -15, position: "absolute"}}
                               error>{props.errors.acceptedTerms}</FormHelperText>}
      </When>
    </>
  );

TermsOfServiceCheckbox.defaultProps = {
  values: {},
  errors: {},
  touched: {},
  submitCount: 0,
  setFieldValue: () => {
  },
  loading: false,
};

export default React.memo(TermsOfServiceCheckbox);
