import {connect} from 'react-redux'
import {getSignInActionCreator} from 'data/redux/actions/authenticationActions';
import SignInForm from 'gui/pages/mainRouter/pages/signIn/components/signInForm/SignInForm';
import {isLoadingSelector} from 'data/redux/selectors/guiSelector';
import {isInvalidLoginSelector} from 'data/redux/selectors/errorsSelector';

const mapStateToProps = (state) => {
  return {
    invalidLogin: isInvalidLoginSelector(state),
    loading: isLoadingSelector(state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onSubmit: (formValues) => dispatch(getSignInActionCreator(formValues))
  };
};

const SignInFormContainer = connect(mapStateToProps, mapDispatchToProps)(SignInForm);

export default SignInFormContainer;
