import React from "react";
import styles from './FacebookLoginButton.module.scss';
import FacebookLogin from 'react-facebook-login/dist/facebook-login-render-props';
import {Button} from '@material-ui/core';
import FacebookIcon from '@material-ui/icons/Facebook';
import {ApiKeys, config, linksMap} from 'data/utils/constants';
import SkeletonPlaceHolder from 'gui/components/skeletonPlaceHolder';

interface IFacebookLoginButtonProps {
  text: string;
  onSubmit: (formValues) => void;
  disabled: boolean;
  onlyAuth?: boolean;
  style?: any;
  onClick?: () => void;
  forceChangeTokenKey?: number;

  loading?: boolean;
}

const FacebookLoginButton = ({forceChangeTokenKey, loading, ...props}: IFacebookLoginButtonProps) => {
  return (
    <FacebookLogin
      appId={ApiKeys.facebookLoginAppId}
      callback={(r) => {
        if (props.onlyAuth) {
          r.onlyAuth = props.onlyAuth;
        }
        props.onSubmit(r);
      }}
      fields=""
      scope=""
      redirectUri={`${config.baseURL}/${linksMap.login}`}
      state={location.pathname}
      render={renderProps => loading ? <SkeletonPlaceHolder
          className={styles.facebookButton}
        /> :
        (

          <Button
            className={`loginButtons ${styles.facebookButton}`}
            fullWidth
            variant="contained"
            onClick={props.onClick || renderProps.onClick}
            disabled={renderProps.disabled || props.disabled}
            style={props.style}
          >
            {props.text}
            <FacebookIcon style={{marginRight: 4}}/>
          </Button>
        )}
    />
  );
};

FacebookLoginButton.defaultProps = {
  disabled: false,
  text: "התחבר עם פייסבוק",
  forceChangeTokenKey: 0,
  loading: false,
};

export default React.memo(FacebookLoginButton);
