import {connect} from 'react-redux'
import {getSignInFacebookActionCreator} from 'data/redux/actions/authenticationActions';
import FacebookLoginButton from 'gui/components/facebookLoginButton';
import {getInitializationTimeStamp, isLoadingSelector} from 'data/redux/selectors/guiSelector';

const mapStateToProps = (state, ownProps) => {
  return {
    forceChangeTokenKey: getInitializationTimeStamp(state),
    loading: isLoadingSelector(state),
    ...ownProps
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onSubmit: (formValues) => dispatch(getSignInFacebookActionCreator(formValues))
  };
};

const FacebookLoginButtonContainer = connect(mapStateToProps, mapDispatchToProps)(FacebookLoginButton);

export default FacebookLoginButtonContainer;
