import React from "react";
import styles from "./VoucherImageSquare.module.scss";
import {ICompanyPrediction, IProduct} from 'data/interfaces/products';
import GenericImageSquare from '../genericImageSquare';
import PercentTicket from 'gui/components/icons/PercentTicket';

import {flightCompanyToLogoUrl} from '../airlineImageSquare/AirlineImageSquare';
import {ASSET} from 'data/redux/constants/assetsConstants';
import {eVoucherTypes} from '../../../../../editors/voucherEditor/components/voucherDetailsForm/components/voucherDetails/components/voucherTypeSelector';

export interface IVoucherImageSquare {
  product: Partial<IProduct>;
  className?: any;
  iconContainerClassName?: string;
  children?: any;
}

const voucherTypeToImageClass = (product: Partial<IProduct>) => {
  switch (product.voucherDetails.type) {
    case eVoucherTypes.flightCompanies:
      return styles.VoucherImageSquarePlane;
    case eVoucherTypes.hotels:
      return styles.VoucherImageSquareHotel;
    case eVoucherTypes.cruise:
      return styles.VoucherImageSquareCruise;
    case eVoucherTypes.agencies:
      return styles.VoucherImageSquareMap;
  }
  return styles.VoucherImageSquareMap;
}

const companyToLogoUrl = (companyDetails: ICompanyPrediction) => {
  if (companyDetails.assets && companyDetails.assets.length) {
    const logoAsset = companyDetails.assets.find(companyAsset => companyAsset.type === ASSET.LOGO);
    if (logoAsset) {
      return logoAsset.asset.sourceLink;
    }
  }
  return null;
}

export const voucherLogo = (product: Partial<IProduct>) => {
  try {
    switch (product.voucherDetails.type) {
      case eVoucherTypes.flightCompanies:
        return flightCompanyToLogoUrl(product.voucherDetails.flightCompanyDetails);
      case eVoucherTypes.hotels:
      case eVoucherTypes.cruise:
      case eVoucherTypes.agencies:
        return companyToLogoUrl(product.voucherDetails.companyDetails);
    }
  } catch (e) {
    console.log("No logo found", e);
  }
  return null;
}


export const voucherText = (product: Partial<IProduct>) => {
  try {
    switch (product.voucherDetails.type) {
      case eVoucherTypes.flightCompanies:
        return product.voucherDetails.flightCompanyDetails.name;
      case eVoucherTypes.hotels:
      case eVoucherTypes.cruise:
      case eVoucherTypes.agencies:
        return product.voucherDetails.companyDetails.name;
    }
  } catch (e) {
    console.log("No name found", e);
  }
  return null;
}


const VoucherImageSquare = (props: IVoucherImageSquare) => (
  <GenericImageSquare
    className={`${styles.VoucherImageSquare} ${voucherTypeToImageClass(props.product)} ${props.className}`.trim()}
    iconContainerClassName={`${props.iconContainerClassName} ${styles.iconContainerClassName}`}
    icon={PercentTicket}
    label="שובר"
    logoSrc={voucherLogo(props.product)}
    logoTitle={voucherText(props.product)}
    children={props.children}
  />
);


VoucherImageSquare.defaultProps = {
  className: '',
  iconContainerClassName: '',
};

export default VoucherImageSquare;
