import {connect} from 'react-redux'
import {getSignInGoogleActionCreator} from 'data/redux/actions/authenticationActions';
import GoogleLoginButton from 'gui/components/googleLoginButton';
import {isLoadingSelector} from 'data/redux/selectors/guiSelector';

const mapStateToProps = (state, ownProps) => {
  return {
    loading: isLoadingSelector(state),
    ...ownProps
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onSubmit: (formValues) => dispatch(getSignInGoogleActionCreator(formValues))
  };
};

const GoogleLoginButtonContainer = connect(mapStateToProps, mapDispatchToProps)(GoogleLoginButton);

export default GoogleLoginButtonContainer;
