import React from 'react';
import {IOrder} from 'data/interfaces/orders';
import Ad from 'gui/components/listPage/components/ad/Ad';
import {hideControl} from 'gui/components/listPage/components/ad/components/adControl';
import {
  ORDER_STATUS_APPROVED_TRANSFER,
  ORDER_STATUS_CANCELED,
  ORDER_STATUS_DISAPPROVED,
  ORDER_STATUS_ORDER_APPROVED_WAITING_PAYMENT,
  ORDER_STATUS_ORDER_APPROVED_WAITING_TRANSFER,
  ORDER_STATUS_ORDER_APPROVED_WAITING_TRANSFER_APPROVAL_BY_BUYER,
  ORDER_STATUS_ORDER_APPROVED_WAITING_TRANSFER_CHARGING_BUYER,
  ORDER_STATUS_ORDER_DISPUTE,
  ORDER_STATUS_ORDER_SOLD,
  ORDER_STATUS_TIMED_OUT,
  ORDER_STATUS_UNKNOWN,
  ORDER_STATUS_WAITING_FOR_APPROVAL
} from 'data/redux/constants/ordersConstants';
import HistoryRoundedIcon from '@material-ui/icons/HistoryRounded';
import AdStatus from 'gui/components/listPage/components/ad/components/adStatus';

interface IOrderAd {
  order: IOrder;
  view?: (id: number) => void
  delete?: (id: number) => void
}


export const statusToText = (status: number): string => {
  switch (status) {
    case ORDER_STATUS_UNKNOWN:
      return "לא ידוע";
    case ORDER_STATUS_WAITING_FOR_APPROVAL:
      return "מחכה לאישור";
    case ORDER_STATUS_DISAPPROVED:
      return "סירב";
    case ORDER_STATUS_CANCELED:
      return "בוטל";
    case ORDER_STATUS_TIMED_OUT:
      return "פג זמן";
    case ORDER_STATUS_ORDER_APPROVED_WAITING_TRANSFER_CHARGING_BUYER:
    case ORDER_STATUS_ORDER_APPROVED_WAITING_TRANSFER:
      return "אושרה וממתינה להעברת בעלות";
    case ORDER_STATUS_ORDER_APPROVED_WAITING_PAYMENT:
      return "אושרה וממתינה לשתלום";
    case ORDER_STATUS_APPROVED_TRANSFER:
      return "אושרה ועברה בעלות";
    case ORDER_STATUS_ORDER_APPROVED_WAITING_TRANSFER_APPROVAL_BY_BUYER:
      return "ממתינה לאישורך";
    case ORDER_STATUS_ORDER_SOLD:
      return "נמכרה";
    case ORDER_STATUS_ORDER_DISPUTE:
      return "מודעה במחלוקת";
  }
}

export const statusAppendTemplate = (status: number): any => {
  switch (status) {
    case ORDER_STATUS_UNKNOWN:
    case ORDER_STATUS_WAITING_FOR_APPROVAL:
    case ORDER_STATUS_ORDER_DISPUTE:
    case ORDER_STATUS_ORDER_APPROVED_WAITING_TRANSFER_CHARGING_BUYER:
    case ORDER_STATUS_ORDER_APPROVED_WAITING_TRANSFER:
    case ORDER_STATUS_ORDER_APPROVED_WAITING_TRANSFER_APPROVAL_BY_BUYER:
      return {text: statusToText(status), template: "waiting", rightIcon: <HistoryRoundedIcon/>};
    case ORDER_STATUS_DISAPPROVED:
    case ORDER_STATUS_CANCELED:
    case ORDER_STATUS_TIMED_OUT:
      return {text: statusToText(status), template: "disapproved"};
    case ORDER_STATUS_ORDER_APPROVED_WAITING_PAYMENT:
      return {text: statusToText(status), template: "default"};
    case ORDER_STATUS_APPROVED_TRANSFER:
    case ORDER_STATUS_ORDER_SOLD:
      return {text: statusToText(status), template: "approved"};
  }
}

const OrderAd = (props: IOrderAd) => {
  return (
    <Ad product={props.order.productDetails}
        hideControl={[hideControl.edit, hideControl.delete]}
        statusComponent={<AdStatus order={props.order} statusToText={statusAppendTemplate}/>}
        delete={props.delete}
    />
  );
};

OrderAd.defaultProps = {
  product: {},

  hideControl: false,
  hideStatus: false,


  view: () => {
  },
  edit: () => {
  },
  delete: () => {
  },
};
export default OrderAd;
