import React from 'react';
import Iframe from 'react-iframe';
import styles from './GoogleMap.module.scss';
import {ApiKeys} from 'data/utils/constants';

interface IGoogleMapProps {
  address: string;
}

const GoogleMap = (props: IGoogleMapProps) => {
  const formattedAddress = props.address.split(",").map(a => a.trim()).filter(a => !(/^\d+$/.test(a))).join(",");
  return (
    <div className={styles.GoogleMap}>
      <Iframe
        url={`https://www.google.com/maps/embed/v1/search?key=${ApiKeys.googleMapKey}&q=${formattedAddress}`}
        height="610px"
        width="100%"
        frameBorder={0}
      />

    </div>
  )
};
GoogleMap.defaultProps = {};
export default React.memo(GoogleMap);
