export interface IHotelSellerCommentsProps {
  product: IProduct;
}

const generateCancellationPrice = product => {
  return "לא ניתן לבטל";
  /*
  if (product && product.hotelDetails && product.hotelDetails.cancelPrice > 0)
    return [numeral(product.hotelDetails.cancelPrice).format("0,0"), currencyValueToLabel[product.hotelDetails.currency]].join(" ")
  return "ללא דמי ביטול"
   */
};

const generateChangePrice = product => {
  if (product && product.hotelDetails && product.hotelDetails.changePrice > 0)
    return [numeral(product.hotelDetails.changePrice).format("0,0"), currencyValueToLabel[product.hotelDetails.currency]].join(" ")
  return "ללא דמי שינוי"
};

const HotelSellerComments = (props: IHotelSellerCommentsProps) => <SellerComments
  title="מידע על המלון"
  description={props.product.hotelDetails.hotelDescription || ''}
  details={[
    {
      title: 'ארוחות',
      value: generateMealText(props.product)
    },
    {
      title: 'דמי ביטול',
      value: generateCancellationPrice(props.product)
    },
    //  {
    //    title: 'דמי שינוי',
    //    value: generateChangePrice(props.product)
    //  },
  ]}
/>;

HotelSellerComments.defaultProps = {
  product: {hotelDetails: {}},
};

export default HotelSellerComments;
