import React from 'react';
import styles from "./Editor.module.scss"
import {MDBCol, MDBContainer, MDBRow} from 'mdbreact';
import {PRODUCT_STATUS_WAITING_FOR_APPROVAL} from 'data/redux/constants/productsConstants';
import Steps from './components/steps/Steps';
import {IProduct} from 'data/interfaces/products';
import ScrollToTopExtended from 'gui/components/scrollToTopExtended/ScrollToTopExtended';
import {Persist} from 'formik-persist';
import {Unless, When} from 'react-if';
import FormikErrorScrollToTop from 'formik-top-error-focus';
import {discardObjectKeys, isObjectWithKeys, trimNumberMinMax, xxHash} from 'data/utils/calculators';
import {versionStamp} from 'data/utils/constants';
import {FormikProps} from 'formik';
import {promiseWait} from 'data/utils/shortcuts';
import {isMaxStep} from './components/abstractDetailsForm/components/stepButtons/components/nextButton';
import PublishedCard from './components/publishedCard';
import {eProductSubType, eProductType} from 'data/enums/products';
import PageWithSidebarStructure from 'gui/components/pageWithSidebarStructure';
import GuaranteesForSeller
  from 'gui/pages/mainRouter/pages/ad/components/commons/preparedAdStructure/components/purchaseForm/components/guaranteesForSeller';


export interface IEditorProps {
  steps: string[];
  product?: IProduct;
  isAdmin?: boolean;

  getStepContent: (number, ...any) => any;
  getProductInfo: (number, ...any) => any;
  onSubmit: (product: Partial<IProduct>) => Promise<any>;

  setUrl: (path: string) => void;

  pathname?: string;

  loading?: boolean;
  productTypeId?: number;
  productType?: eProductType;
  productSubType?: eProductSubType;
  loadProductTypes?: (productType: number, productSubType: number) => void;

}

/**
 * Filters product keys
 * @param product
 */
const filterPublishProductKeys = (product: Partial<IProduct>): Partial<IProduct> => {
  const filteredProduct: Partial<IProduct> = {};
  product = discardObjectKeys(product,
    (key, value) => ['submittedProduct'].includes(key),
  );
  for (const key in product) {
    if (product[key] && (isObjectWithKeys(product[key]) || ['id', 'product_type_id', 'productIdentifier', 'status', 'submittedProduct'].includes(key))) {
      filteredProduct[key] = product[key];
      if (isObjectWithKeys(filteredProduct[key])) {
        delete filteredProduct[key].activeStep;
      }
    }
  }
  return filteredProduct;
}

const initialState = {
  activeStep: 0,
  loading: false,
  submittedProduct: {},
};

class Editor extends React.PureComponent<IEditorProps, any> {
  public static defaultProps = {
    getStepContent: () => null,
    getProductInfo: () => null,
    onSubmit: () => null,
    setUrl: () => null,
  };
  getInitialState = (additions = {}) => {
    return {
      ...(this.props.product || {}),
      ...initialState,
      ...additions,
    };
  };

  state = this.getInitialState();


  static stepFromProps(props) {
    if (props.pathname) {
      const step = props.pathname.split("step");
      if (step.length > 0) {
        const activeStep = step
          .map(s => s.replace(/\D+/g, ''))
          .filter(s => s)
          .find(s => !isNaN(s.replace(/\D+/g, '')));
        if (activeStep) {
          return trimNumberMinMax(parseInt(activeStep) - 1);
        }
      }
    }
    return -1;
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    const activeStep = Editor.stepFromProps(nextProps);
    if (typeof activeStep === 'number' && activeStep >= 0) {
      if (activeStep > 0) {
        for (const key in prevState) {
          if ((key in prevState) && typeof prevState[key] === 'object' && Object.keys(prevState[key]).length > 0) {
            return {activeStep};
          }
        }
      }
      return {activeStep: 0};
    }
    return null;
  }

  setUrlToStep = activeStep => {
    const currentActiveStep = Editor.stepFromProps(this.props);
    if (currentActiveStep >= 0 && typeof this.props.setUrl === 'function') {
      if (activeStep !== currentActiveStep) {
        this.props.setUrl(this.currentRoute(activeStep));
      }
    }
  }

  componentDidUpdate(prevProps, prevState) {
    const activeStep = Editor.stepFromProps(prevProps);
    if (activeStep && activeStep > prevState.activeStep) {
      this.setUrlToStep(prevState.activeStep);
    }
    /*
    if (this.props.loading&&!this.props.productTypeId && this.props.loadProductTypes && this.props.productType && this.props.productSubType) {
      this.props.loadProductTypes(this.props.productType, this.props.productSubType);
    }

     */
  }

  getProduct = (): Partial<IProduct> => {
    const {activeStep, ...p} = this.state;
    return filterPublishProductKeys(p);
  }

  moveNextStep = ({activeStep, ...data}) => {
    return new Promise(resolve => this.setState({
      loading: true,
      activeStep: Math.min(activeStep, this.props.steps.length - 1), ...data
    }, resolve as any))
      .then(r => {
        this.setUrlToStep(activeStep);
        if (activeStep > 2) {
          return this.props.onSubmit(
            {
              ...filterPublishProductKeys({
                ...(this.props.product || {}),
                ...this.getProduct(),
                ...(this.props.isAdmin ? {
                  status: this.getProduct().status || PRODUCT_STATUS_WAITING_FOR_APPROVAL
                } : {}),
              }),
              product_type: this.props.productType,
              product_sub_type: this.props.productSubType
            }
          )
            .then(s => {
              if (s.error) {
                return this.moveNextStep({activeStep: 2,});
              } else {
                this.clearAllSavedFormStates();
                return new Promise(resolve => this.setState(this.getInitialState({submittedProduct: {...this.state, ...s.value}}), resolve as any));
              }
            })
            .catch(e => {
              //   return this.moveNextStep({activeStep: 2,});
              return Promise.resolve();
            })
        }
        return r;
      })
      .catch(e => Promise.resolve())
      .then(() => promiseWait(500))
      .then(() => new Promise(resolve => this.setState({loading: false}, resolve as any)));
  }

  setAdminStep = (activeStep) => {
    if (this.props.isAdmin) {
      this.setState({
        activeStep
      });
    }
  }

  public static currentRoute = (pathName: string, step: number) => `/${pathName.split("step")[0].split("/").map(s => (s || '').trim()).filter(s => s).join("/")}/step/${step + 1}`

  currentRoute = (step: number = this.state.activeStep) => Editor.currentRoute(location.pathname, step)

  currentEditorKey = (step?: number) => `Editor-${versionStamp}-${xxHash(this.currentRoute(step))}`

  clearAllSavedFormStates = () => {


    for (let i = 0; i < 10; i++) {
      try {
        localStorage.removeItem(this.currentEditorKey(i));
      } catch (e) {
      }
    }

    promiseWait(500)
      .then(() => {
        this.state = initialState;
      });
  }

  private static persist({isSubmitting, ...filteredData}: FormikProps<{}>) {
    return {
      ...filteredData,
      // values: {},
      touched: {},
      errors: {},
      submitCount: 0,
      initialErrors: {},
      initialTouched: {},
      initialValues: {},
      validateOnMount: false,
      validateOnBlur: false,
      validateOnChange: false,
      dirty: false,
    };
  }

  private getStepProductInfo = () => {
    if (isMaxStep({steps: this.props.steps, activeStep: this.state.activeStep - 1})) {
      return (null)
    } else
      return this.props.getProductInfo(this.state.activeStep, {
        commonFormComponents: this.commonFormComponents,
        ...this.props,
        activeStep: this.state.activeStep,
        product: {
          ...(this.props.product || {}),
          ...this.getProduct(),
        },
        loading: this.state.loading || this.props.loading,
      }, this.moveNextStep);
  }

  private getStepContent = () => {
    if (isMaxStep({steps: this.props.steps, activeStep: this.state.activeStep - 1})) {
      return (<PublishedCard product={this.state.submittedProduct} loading={this.state.loading || this.props.loading}/>)
    } else
      return this.props.getStepContent(this.state.activeStep, {
        commonFormComponents: this.commonFormComponents,
        ...this.props,
        activeStep: this.state.activeStep,
        product: {
          ...(this.props.product || {}),
          ...this.getProduct(),
        },
        loading: this.state.loading || this.props.loading,
      }, this.moveNextStep);
  }

  commonFormComponents = (inputs: any = {}) => <>
    <Unless condition={this.props.product && this.props.product.id > 0}>
      {() =>
        <When condition={this.state.activeStep <= 0}>
          {() => <Persist name={this.currentEditorKey()} persistFilter={Editor.persist}/>}
        </When>
      }
    </Unless>
    <FormikErrorScrollToTop/>
    
  </>

  render() {
    const currentRoute = this.currentRoute();
    return (
      <PageWithSidebarStructure
        className={styles.editorStructure}
        navigation={
          <div className={styles.topPoints}>
            <GuaranteesForSeller
              aboveComponents={this.getStepProductInfo()}
            />
          </div>
        }>
        <MDBContainer fluid className="createProductContainer">
          <MDBRow>
            <MDBCol className={styles.editorSection}>
              <Steps isAdmin={this.props.isAdmin}
                     activeStep={this.state.activeStep}
                     setAdminStep={this.setAdminStep}
                     steps={this.props.steps}/>
              {this.getStepContent()}
            </MDBCol>
          </MDBRow>
          <ScrollToTopExtended scollOnChange={this.state.activeStep} noButton scrollOnRender smooth/>
        </MDBContainer>
      </PageWithSidebarStructure>
    );
  }
}

export default Editor;
