import {connect} from 'react-redux'
import {AuthenticatedMapDispatchToProps, AuthenticatedMapStateToProps} from 'data/redux/containers/commons/Commons';
import {getInsertProductActionCreator, getProductTypeId} from 'data/redux/actions/productsActions';
import CreateProduct from 'gui/pages/controlRouter/pages/createProduct';
import {navigateIfNotThere} from 'data/redux/actions/navigationActions';
import {getPathName} from 'data/redux/selectors/routerSelector';
import {isLoadingSelector} from 'data/redux/selectors/guiSelector';
import {getProductTypeIdSelector} from 'data/redux/selectors/productSelector';


const mapStateToProps = (state) => {
  return {
    productTypeId: getProductTypeIdSelector(state),
    pathname: getPathName(state),
    loading: isLoadingSelector(state),
    ...AuthenticatedMapStateToProps(state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    loadProductTypes: (type, subType) => dispatch(getProductTypeId(type, subType)),
    onSubmit: (product) => dispatch(getInsertProductActionCreator(product))
      .then(r => {
        //   dispatch(navigateIfNotThere(linksMap.productsByMeOpen));
        return r;
      })
      .catch(e => {
        
        return Promise.resolve({error: true});
      }),
    setUrl: (path: string) => dispatch(navigateIfNotThere(path)),
    ...AuthenticatedMapDispatchToProps(dispatch)
  };
};

const CreateProductContainer = connect(mapStateToProps, mapDispatchToProps)(CreateProduct);

export default CreateProductContainer;

