import React from "react";
import styles from "./VoucherTypeSelector.module.scss";
import FormikSearchSelector from 'gui/components/formikSearchSelector/FormikSearchSelector';
import {SelectOption} from 'react-select-material-ui';

export interface ISelectorProps {
  label: string;
  voucherTypes: SelectOption[];
}


const VoucherTypeSelector = (props: ISelectorProps) => {
  return (
    <FormikSearchSelector
      className={styles.voucherTypeSelector}
      options={props.voucherTypes}
      {...props}
    />
  );
};

export enum eVoucherTypes {
  flightCompanies = 1,
  agencies = 2,
  hotels = 3,
  cruise = 4,
}

export const voucherTypes = [
  "שובר לחברת תעופה"
  , "שובר לסוכנות נסיעות"
  , "שובר למלון"
  , "שובר לשייט"
]
  .map((voucherType, index) => ({label: `${voucherType}`, value: index + 1}));

VoucherTypeSelector.defaultProps = {
  voucherTypes
};

export default React.memo(VoucherTypeSelector);
