import React from "react";
import NumberFormat from "react-number-format";
import {FastField} from 'formik';
import {unicodeToChar} from 'data/utils/shortcuts';
import styles from './FormikPhoneInput.module.scss';
import SkeletonPlaceHolder from 'gui/components/skeletonPlaceHolder';
import CustomTextField from "../customTextField/CustomTextField";

interface IFormikPhoneInputProps {
  name: string;
  label: string | any;
  loading?: boolean;
}


interface NumberFormatCustomProps {
  inputRef: (instance: NumberFormat | null) => void;
  onChange: (event: { target: { name: string; value: string } }) => void;
  name: string;
}

const NumberFormatCustom = (props: NumberFormatCustomProps, a) => {
  const {inputRef, onChange, ...other}: any = props;
  if (other.value && other.value.trim().length > 0) {
    other.value = `${parseInt(other.value)}`;
  }
  return (
    <NumberFormat
      {...other}
      style={{direction: 'ltr'}}
      getInputRef={inputRef}
      onValueChange={(values) => {
        onChange({
          target: {
            name: props.name,
            //value: values.value.startsWith("0") ? values.value : "0",
            value: values.value
          },
        });
      }}
      allowLeadingZeros={false}
      allowNegative={false}
      isNumericString
      format={`+972 0${unicodeToChar("\u200b")}##-#######`}
      mask="_"
      allowEmptyFormatting
    />
  );
}

const CustomizedTextField = (props: any) => {
  const touched = props.form.touched[props.field.name];
  const error = touched && props.form.errors[props.field.name];
  const valid = touched && !error;

  return (
    <CustomTextField
      {...props}
      {...props.field}
    />
  );
}

const FormikPhoneInput = ({loading, ...props}: IFormikPhoneInputProps) => loading ?
  (<SkeletonPlaceHolder
    className={styles.loadingField}
  >
    {props.label}
  </SkeletonPlaceHolder>) : (
    <FastField
      component={CustomizedTextField}
      InputProps={{
        inputComponent: NumberFormatCustom as any,
      }}
      required
      fullWidth
      variant="outlined"
      id={props.name}
      {...props}
    />
  );

FormikPhoneInput.defaultProps = {
  name: "",
  label: "",
  loading: false,
};

export default FormikPhoneInput;
