import React from 'react';
import styles from './Ad.module.scss';
import {MDBCard, MDBCardBody, MDBCol, MDBRow} from 'mdbreact';
import {IProduct} from 'data/interfaces/products';
import {
  hotelDetails,
  identifyProductCategoryType,
  ticketDetails,
  vacationDetails,
  voucherDetails
} from 'data/redux/constants/productsConstants';
import AdFlight from './components/adFlight/AdFlight';
import AdHotel from './components/adHotel/AdHotel';
import AdVacation from './components/adVacation/AdVacation';
import AdRichData from './components/adRichData';
import AdCardStructureContainer from 'data/redux/containers/adCardStructureContainer';
import AdVoucher from './components/adVoucher/AdVoucher';

interface IAdProps {
  product: IProduct;

  hideControl?: boolean | any;
  hideStatus?: boolean;
  showPurchase?: boolean;

  statusToText?: (status: number) => string;
  status?: number;
  statusComponent?: any;

  view?: (id: number) => void
  edit?: (id: number) => void
  delete?: (id: number) => void

  loading?: boolean;
}

const selectProductType = (props: IAdProps) => {

  switch (identifyProductCategoryType(props.product)) {
    case ticketDetails:
      return (<AdFlight {...props} />);
    case hotelDetails:
      return (<AdHotel {...props} />);
    case vacationDetails:
      return (<AdVacation {...props} />);
    case voucherDetails:
      return (<AdVoucher {...props} />);
  }
  
  return null;
}

const Ad = (props: IAdProps) => {
  const cardContent = props.loading ? <AdCardStructureContainer {...props}/> : selectProductType(props);
  if (cardContent) {
    return (
      <MDBRow tag="article" itemScope itemProp="offer" itemType="http://schema.org/Offer" className={styles.adCard}>
        <MDBCol lg="12" xl="12">
          <MDBCard>
            <MDBCardBody>
              <AdRichData product={props.product}/>
              {cardContent}
            </MDBCardBody>
          </MDBCard>
        </MDBCol>
      </MDBRow>
    );
  }
  return null;
};

Ad.defaultProps = {
  product: {saleDetails: {}},
  hideControl: false,
  hideStatus: false,

  view: () => {
  },
  edit: () => {
  },
  delete: () => {
  },
};
export default Ad;
