import React from 'react';
import styles from "./ChooseEditorTypeButton.module.scss";
import LinkBlockContainer from 'data/redux/containers/linkBlockContainer';
import Editor from 'gui/components/editors/commons/editor';


export interface IChooseEditorTypeButtonProps {
  link?: string;
  label?: string;
  icon?: any;
}

const activeToLogic = (to, compareTo) => to.split("/").filter(s => isNaN(s)).join("/").trim() === compareTo.split("/").filter(s => isNaN(s)).join("/").trim();

const ChooseEditorTypeButton = (props: IChooseEditorTypeButtonProps) =>
  (<LinkBlockContainer className={styles.ChooseEditorTypeButton}
                       to={Editor.currentRoute(props.link, 0)}
                       activeToLogic={activeToLogic}>
    <span className={styles.label}>  {props.label}</span>
    {props.icon ? <props.icon/> : null}
  </LinkBlockContainer>)

ChooseEditorTypeButton.default = {
  label: '',
  link: ','
};
export default ChooseEditorTypeButton;
