import React from "react";
import styles from "./FormikCustomInput.module.scss";
import {FastField} from 'formik';
import Label from './components/label';

import {Unless, When} from 'react-if';
import {FormHelperText} from '@material-ui/core';

export interface IPriceInputProps {
  name: string;
  label: string | any;
  InputProps?: any;
  inputComponent?: any;
  hideLabel?: boolean;
}

const CustomizedTextField = (props: any) => {
  const touched = props.form.touched[props.field.name];
  const error = touched && props.form.errors[props.field.name];
  const value = props.field.value;
  const valid = touched && !error || (isNaN(value) ? false : value > props.min);
  return (
    <div className={`${styles.CustomizedTextField} ${error ? styles.fieldError : ''}`.trim()}>
      <Unless condition={props.hideLabel}>
        {() => <Label label={props.label}/>}
      </Unless>
      {typeof props.inputComponent === 'function' ? props.inputComponent(props) : props.inputComponent}
      <div className={styles.SearchSelectErrorsContainer}>
        <When condition={error !== undefined}>{() =>
          <FormHelperText
            className={styles.SearchSelectError}
            error>{error}
          </FormHelperText>
        }
        </When>
      </div>
    </div>
  );
}

const FormikCustomInput = (props: IPriceInputProps) => (
  <FastField
    component={CustomizedTextField}
    id={props.name}
    {...props}
  />
);

FormikCustomInput.defaultProps = {
  name: "",
  label: "",
  inputComponent: '',
  hideLabel: false,
};

export default FormikCustomInput;
