import React from 'react';
import DateRangeIcon from '@material-ui/icons/DateRange';
import GenericDetail from '../genericDetail';
import moment from 'moment';

interface IAdArrivalDateProps {
  startDate?: number;
}

const AdArrivalDate = (props: IAdArrivalDateProps) => (
  <GenericDetail
    title="תאריך הגעה"
    icon={DateRangeIcon}
  >
    {moment(props.startDate).format('MMM D YYYY')}
  </GenericDetail>
);

AdArrivalDate.defaultProps = {
  startDate:0,
};
export default AdArrivalDate;
