import React from 'react';
import styles from './HotelLocationCard.module.scss'
import {IProduct} from 'data/interfaces/products';
import InfoCard from 'gui/pages/mainRouter/pages/ad/components/commons/infoCard';
import LocationOnRoundedIcon from '@material-ui/icons/LocationOnRounded';
import GoogleMap from 'gui/components/googleMap';
import ShowTheWay from 'gui/components/showTheWay';
import {productLatitudeSelector, productLongitudeSelector} from 'data/redux/selectors/productSelector';
import AdStreet from 'gui/components/listPage/components/ad/components/adStreet';

interface IFirstPageProps {
  product?: IProduct;
}

const HotelLocationCard = (props: IFirstPageProps) => (
  <InfoCard
    title="כתובת"
    icon={LocationOnRoundedIcon}
    toolTip="כתובת"
    className={styles.HotelLocationCard}
  >
    <div className={styles.actions}>
      <AdStreet hotelDetails={props.product.hotelDetails} className={styles.adStreet}/>
      <ShowTheWay className={styles.showtheway}
                  latitude={productLatitudeSelector(props.product)}
                  longitude={productLongitudeSelector(props.product)}/>
    </div>
    <GoogleMap address={props.product.hotelDetails.hotel.formatted_address}/>
  </InfoCard>
);

HotelLocationCard.defaultProps = {};

export default React.memo(HotelLocationCard);
