import React, {useState} from 'react';
import lazyWithPreload from 'lazy-with-preload';
import styles from './Ad.module.scss';
import {IAuthenticatedPageProps} from 'gui/components/authenticatedPage/AuthenticatedPage';
import {IProduct} from 'data/interfaces/products';
import {
  hotelDetails,
  identifyProductCategoryType,
  ticketDetails,
  vacationDetails,
  voucherDetails
} from 'data/redux/constants/productsConstants';
import {IUser} from 'data/redux/reducers/authenticationReducer';
import {ITransaction} from 'data/redux/reducers/transactionsReducer';
import {IOrder, IOrderOptions} from 'data/interfaces/orders';
import LoaderContainer from 'data/redux/containers/LoaderContainer/LoaderContainer';
import {If, Then} from 'react-if';
import {Route, Switch} from 'react-router';
import {linksMap} from 'data/utils/constants';
import EditProductContainer from 'data/redux/containers/editProductContainer';
import {generateSluggedAdUrl} from 'data/redux/actions/guiActions';
import AdControlTopLineContainer from 'data/redux/containers/adControlTopLineContainer/AdControlTopLineContainer';
import ScrollToTopExtended from 'gui/components/scrollToTopExtended/ScrollToTopExtended';
import AdLoading from 'gui/pages/mainRouter/pages/ad/components/adLoading';
import CrispChatContainer from 'data/redux/containers/crispChatContainer';

/**
 import AdHotel from './components/adHotel/AdHotel';
 import AdVoucher from './components/adVoucher/AdVoucher';
 import AdFlight from './components/adFlight/AdFlight';
 import AdVacation from './components/adVacation/AdVacation';
 */

const AdHotel = lazyWithPreload(() => import(/* webpackChunkName: "AdHotel" , webpackMode:'lazy' */ './components/adHotel/AdHotel'));
const AdVoucher = lazyWithPreload(() => import(/* webpackChunkName: "AdVoucher" , webpackMode:'lazy' */ './components/adVoucher/AdVoucher'));
const AdFlight = lazyWithPreload(() => import(/* webpackChunkName: "AdFlight" , webpackMode:'lazy' */ './components/adFlight/AdFlight'));
const AdVacation = lazyWithPreload(() => import(/* webpackChunkName: "AdVacation" , webpackMode:'lazy' */ './components/adVacation/AdVacation'));

interface IAdProps extends IAuthenticatedPageProps {
  product?: IProduct;
  order?: IOrder;
  transaction?: ITransaction;
  user?: IUser;
  id?: number;
  slug?: string;
  allowNavigation?: boolean;
  isProductOwner?: boolean;
  isOrderOwner?: boolean;

  beginOrder?: (product: IProduct, options: IOrderOptions) => Promise<void>;
  loadAdPage?: (id: number, slug?: string) => Promise<IProduct>;
  loadAllOrdersToProduct?: (product_id: number) => Promise<IOrder[]>;
  loadAllOrdersByMe?: () => Promise<any>;
  autoFixSlug?: (product: IProduct) => Promise<IProduct>;
}

const getProductPageComponentByType = (props: IAdProps) => {
  switch (identifyProductCategoryType(props.product)) {
    case ticketDetails:
      return AdFlight;
    case hotelDetails:
      return AdHotel;
    case vacationDetails:
      return AdVacation;
    case voucherDetails:
      return AdVoucher;
  }
  return (null);
}

export const selectProductType = (props: IAdProps) => {
  const ProductPageComponent = getProductPageComponentByType(props);
  if (ProductPageComponent) {
    return (<ProductPageComponent {...props} />);
  }
  return (<div/>);
}

const preloadProductPage = product => {
  const ProductPageComponent = getProductPageComponentByType({product} as any);
  if (ProductPageComponent) {
    return ProductPageComponent
      .preload()
      .then(() => product);
  }
  return product;
}

const Ad = (props: IAdProps) => {
  const [isFetchingProduct, setFetchingProduct] = useState(false);
  const [nextEditorRef, setNextEditorRef] = useState(true);

  React.useEffect(() => {
    if (!isFetchingProduct && props.id && props.allowNavigation && props.product && props.product.id !== props.id) {
      setFetchingProduct(true);
      props.loadAdPage(props.id)
        .then(product => props.loadAllOrdersToProduct(product.id).then(() => product))
        .then(preloadProductPage)
        .then((product) => props.autoFixSlug(product))
        .catch(e => {
          
          return Promise.resolve();
        })
        .then(s => {
          setFetchingProduct(false);
        });
    }

  }, [props.allowNavigation, props.product, props.id]);
  return (
    <>
      <AdControlTopLineContainer nextEditorRef={nextEditorRef}/>
      <section className={`container-xl ${styles.AdContainer}`}>
        <CrispChatContainer/>
        <ScrollToTopExtended
          bottom={95}
          scollOnChange={JSON.stringify({
            orderStatus: (props.order || {}).status,
            productStatus: (props.product || {}).status
          })}
          scrollOnRender smooth/>
        <LoaderContainer
          ignoreRedux={true}
          loading={isFetchingProduct || !props.product || props.product.id !== props.id || !props.product.productIdentifier}
          loadingPlaceholder={
            <AdLoading/>}
        >
          <Switch>
            <Route exact path={linksMap.adIdSlug}>
              <If condition={props.product.id === props.id}>
                <Then>{() =>
                  selectProductType(props)
                }</Then>
              </If>
            </Route>
            <Route exact path={linksMap.adEditIdSlug}>
              <EditProductContainer onSubmitRedirectTo={generateSluggedAdUrl(props.product)}
                                    setSubmitRef={r => setNextEditorRef(r)}/>
            </Route>
          </Switch>
        </LoaderContainer>
      </section>
    </>
  );
};

Ad.defaultProps = {
  product: {},
  loadAdPage: (id, slug) => new Promise(resolve => resolve({id, slug})),
  loadAllOrdersToProduct: (product_id) => new Promise(resolve => resolve([])),
  loadAllOrdersByMe: (id, slug) => new Promise(resolve => resolve({id, slug})),
  autoFixSlug: (product) => new Promise(resolve => resolve({product})),
};

export default Ad;
