import React from 'react';
import styles from './CreateProduct.module.scss';
import {MDBCol, MDBContainer, MDBRow} from 'mdbreact';
import CardGiftCardIcon from '@material-ui/icons/CardGiftcard';
import FlightIcon from '@material-ui/icons/Flight';
import HotelIcon from '@material-ui/icons/Hotel';
import {Route, Switch} from 'react-router';
import AuthenticatedPage from 'gui/components/authenticatedPage/AuthenticatedPage';
import OpenGiftIcon from 'gui/components/icons/OpenGiftIcon';
import {editorTypes, linksMap} from 'data/utils/constants';
import {IProduct} from 'data/interfaces/products';
import FlightEditor from 'gui/components/editors/flightEditor';
import HotelEditor from 'gui/components/editors/hotelEditor';
import VacationEditor from 'gui/components/editors/vacationEditor';
import VoucherEditor from 'gui/components/editors/voucherEditor';
import HowItWorksLink from './components/howItWorksLink';
import ChooseOfferTypeTitle from './components/chooseOfferTypeTitle';
import ChooseEditorTypeButtons from './components/chooseEditorTypeButtons';
import Editor from 'gui/components/editors/commons/editor';
import PageWithSidebarStructure from 'gui/components/pageWithSidebarStructure/PageWithSidebarStructure';
import CrispChatContainer from 'data/redux/containers/crispChatContainer/CrispChatContainer';
import SellerPoints
  from 'gui/pages/mainRouter/pages/ad/components/commons/preparedAdStructure/components/purchaseForm/components/guaranteesForSeller/components/sellerPoints';


interface ICreateProductProps {
  onSubmit: (product: IProduct) => Promise<any>;
  navigateOnLogout: () => void;
  loading?: boolean;
}

const activeToLogic = (to, compareTo) => to.split("/").filter(s => isNaN(s)).join("/").trim() === compareTo.split("/").filter(s => isNaN(s)).join("/").trim();

const showChoiceButtonsPaths = [
  ...editorTypes,
  ...editorTypes.map(type => Editor.currentRoute(type, 0))

];


class CreateProduct extends AuthenticatedPage<ICreateProductProps> {
  render() {
    return (
      <MDBContainer fluid className={`createProductContainer ${styles.createProductContainer}`}>
        <CrispChatContainer/>
        <PageWithSidebarStructure className={styles.offerTypeContainer}>
          <SellerPoints className={'showWhen767'}/>
          <Switch>
            <Route exact path={showChoiceButtonsPaths}>
              <div className={styles.top}>
                <ChooseOfferTypeTitle/>
                <HowItWorksLink/>
              </div>
              <MDBRow className={styles.buttonsContainer}>
                <MDBCol>
                  <ChooseEditorTypeButtons
                    links={[
                      {
                        link: linksMap.hotelEditor,
                        label: 'מלון',
                        icon: HotelIcon
                      },
                      {
                        link: linksMap.flightEditor,
                        label: 'טיסה',
                        icon: FlightIcon
                      },
                      {
                        link: linksMap.vacationEditor,
                        label: 'חבילת חופשה',
                        icon: OpenGiftIcon
                      },
                      {
                        link: linksMap.voucherEditor,
                        label: 'שובר',
                        icon: CardGiftCardIcon
                      },
                    ]}
                  />
                </MDBCol>
              </MDBRow>
            </Route>
          </Switch>
        </PageWithSidebarStructure>
        <MDBRow className={styles.editorsRow}>
          <MDBCol xl="12" style={{padding: 0}}>
            <Switch>
              <Route path={linksMap.flightEditor}>
                <FlightEditor {...this.props}/>
              </Route>
              <Route path={linksMap.hotelEditor}>
                <HotelEditor {...this.props}/>
              </Route>
              <Route path={linksMap.vacationEditor}>
                <VacationEditor {...this.props}/>
              </Route>
              <Route path={linksMap.voucherEditor}>
                <VoucherEditor {...this.props}/>
              </Route>
            </Switch>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    );
  }
}


export default CreateProduct;
