import React from 'react';
import {FastField, FormikErrors} from 'formik';
import EditorSection
    from 'gui/components/editors/commons/editor/components/editorSection';
import {Grid} from '@material-ui/core';
import VoucherTypeSelector, {eVoucherTypes} from 'gui/components/editors/voucherEditor/components/voucherDetailsForm/components/voucherDetails/components/voucherTypeSelector';
import {MDBPopover, MDBPopoverBody} from 'mdbreact';
import {Case, Switch} from 'react-if';
import CompanyAutoCompleteInputContainer from 'data/redux/containers/companyAutoCompleteInputContainer';
import AirlineSelectorContainer from 'data/redux/containers/airlineSelectorContainer';
import FormikDateSelector from 'gui/components/formikDateSelector';
import FormikDescription from 'gui/components/formikDescription';


export interface IVoucherDetails {
    type: eVoucherTypes,
}

const VoucherDetails = (props: { values: IVoucherDetails, errors: FormikErrors<any>, setFieldValue: any, touched: any }) => (
    <EditorSection title="פרטי שובר">
        <Grid item xs={12} sm={4}>
            <FastField
                component={VoucherTypeSelector}
                label="סוג השובר"
                name="type"/>
        </Grid>
        <Grid item xs={12} sm={4}>
            <MDBPopover
                placement="top"
                popover
                clickable
                domElement={true}
            >
                <div>
                    <Switch>
                        <Case condition={props.values.type === eVoucherTypes.agencies}>{() =>
                            <FastField
                                component={CompanyAutoCompleteInputContainer}
                                label="חברה (יש לרשום באנגלית) *"
                                name="companyDetails"
                                onChange={(e) => {
                                    
                                    props.setFieldValue('companyDetails', e);
                                }}
                            />
                        }
                        </Case>
                        <Case condition={props.values.type === eVoucherTypes.cruise}>{() =>
                            <FastField
                                component={CompanyAutoCompleteInputContainer}
                                label="חברה (יש לרשום באנגלית) *"
                                name="companyDetails"
                            />
                        }
                        </Case>
                        <Case condition={props.values.type === eVoucherTypes.flightCompanies}>{() =>
                            <FastField
                                component={AirlineSelectorContainer}
                                label="חברה (יש לרשום באנגלית) *"
                                name="flightCompanyDetails"
                            />
                        }
                        </Case>
                        <Case condition={props.values.type === eVoucherTypes.hotels}>{() =>
                            <FastField
                                component={CompanyAutoCompleteInputContainer}
                                label="חברה (יש לרשום באנגלית) *"
                                name="companyDetails"
                            />
                        }
                        </Case>

                    </Switch>
                </div>
                <div>
                    <MDBPopoverBody>
                        לדוגמא ISSTA, EL AL ועוד...
                    </MDBPopoverBody>
                </div>
            </MDBPopover>
        </Grid>
        <Grid item xs={12} sm={4}>
            <FastField
                component={FormikDateSelector}
                label="תאריך תפוגה"
                name="expiration"/>
        </Grid>
        <Grid item xs={12}>
            <FormikDescription
                label="תאור השובר- זה המקום לתת כמה שיותר מידע על השובר כדי שיהיה ברור לקונים מה בדיוק אתם מציעים"
                name="description"
                values={props.values}
            />
        </Grid>
        <Grid item xs={12} style={{marginTop: 20}}>
            <FormikDescription
                label="מימוש השובר - איך ניתן לממש את השובר ום יש תנאים מסוימים שחשוב לדעת"
                name="conditions"
                values={props.values}
            />
        </Grid>

    </EditorSection>
);

VoucherDetails.defaultProps = {
    values: {},
    errors: [],
    touched: {},
    forcedRoundTrip: false,
    setFieldValue: () => null,
};

export default React.memo(VoucherDetails);
