import React from "react";
import styles from './HotelImagesSquare.module.scss';
import {IProduct} from 'data/interfaces/products';
import {config} from 'data/utils/constants';
import {ASSET} from 'data/redux/constants/assetsConstants';
import {SizeMe} from "react-sizeme";
import Gallery from "./components/gallery/Gallery";


export interface IHotelImagesSquare {
  product: IProduct;
  style?: any;
  showIcon?: boolean;
  children?: any;
}

const generateAssetList = (product: IProduct) => {
  if (Array.isArray(product.assets)) {
    return product.assets
      .filter(({type}) => type === ASSET.PLACE);
  } else if (Array.isArray(product.hotelDetails.imagePick)) {
    return product.hotelDetails.imagePick
      .map((sourceLink) => ({asset: {sourceLink}}));
  }
  return [];
}

const HotelImagesSquare = (props: IHotelImagesSquare) => (
  <SizeMe>{({size: {width}}) => {
    const isMobile = width <= 680;
    return (
      <Gallery
        images={generateAssetList(props.product)
          .map(({asset: {sourceLink}}) => ({
            fullscreen: `${config.baseURL}/placephoto/${sourceLink}?maxwidth=${1920}`,
            original: `${config.baseURL}/placephoto/${sourceLink}?maxwidth=${802}`,
            thumbnail: `${config.baseURL}/placephoto/${sourceLink}?maxwidth=${85}`,
            originalAlt: props.product.hotelDetails.hotel.name,
            thumbnailAlt: props.product.hotelDetails.hotel.name,
          }))}
        galleryOverrides={{
          additionalClass: `${styles.HotelImagesBigSquare} ${isMobile ? styles.mobile : ''}`.trim()
        }}
        overlay={props.children}
        width={width}

      />);
  }
  }</SizeMe>
);

HotelImagesSquare.defaultProps = {
  showIcon: true,
  style: {},
};

export default HotelImagesSquare;
