import React from 'react';
import {FastField} from 'formik';
import styles from './HotelDetails.module.scss';
import {Grid} from '@material-ui/core';
import {MDBPopover, MDBPopoverBody} from 'mdbreact';
import AutoCompletePlacesSelectorContainer
  from 'data/redux/containers/autoCompletePlacesSelectorContainer/AutoCompletePlacesSelectorContainer';
import {When} from 'react-if';
import {FormikErrors} from 'formik/dist/types';
import {IHotelInfo} from 'data/interfaces/products';
import CustomTextField from 'gui/components/customTextField';
import HotelDescription from './components/hotelDescription';
import PlaceImagePicker from './components/placeImagePicker/PlaceImagePicker';
import EditorSection from '../../../../../commons/editor/components/editorSection';

const HotelDetails = (props: { values: IHotelInfo, submitCount: number, errors: FormikErrors<any>, setFieldValue: any }) => (
  <EditorSection title="פרטי מלון">
    <Grid item xs={12} style={{paddingBottom: 0}}>
      <MDBPopover
        placement="top"
        popover
        clickable
        domElement={true}
      >
        <div>
          <FastField
            component={AutoCompletePlacesSelectorContainer}
            label="בית מלון *"
            name="hotel"/>
        </div>
        <div>
          <MDBPopoverBody>
            בחר/י בית מלון
          </MDBPopoverBody>
        </div>
      </MDBPopover>
    </Grid>
    <When condition={props.values.hotel !== undefined}>
      {() => <Grid container spacing={2} className={styles.hotelDetails}
                   style={{paddingLeft: 8, paddingRight: 8, marginTop: -19}}>
        <Grid item sm={!!(props.values.hotel.loading || props.values.hotel && props.values.hotel.country) || "auto"}>
          <CustomTextField
            label="מדינה"
            value={props.values.hotel ? props.values.hotel.country : ""}
            disabled={props.values.hotel === undefined}
            readOnly
            loading={props.values.hotel.loading}
          />
        </Grid>
        <Grid item sm={!!(props.values.hotel.loading || props.values.hotel && props.values.hotel.city) || "auto"}>
          <CustomTextField
            label="עיר"
            value={props.values.hotel ? props.values.hotel.city : ""}
            disabled={props.values.hotel === undefined}
            readOnly
            loading={props.values.hotel.loading}
          />
        </Grid>
        <Grid item
              sm={!!(props.values.hotel.loading || props.values.hotel && props.values.hotel.street) || "auto"}>
          <CustomTextField
            label="רחוב"
            value={props.values.hotel ? props.values.hotel.street : ""}
            disabled={props.values.hotel === undefined}
            readOnly
            loading={props.values.hotel.loading}
          />
        </Grid>
        <Grid item
              sm={!!(props.values.hotel.loading || props.values.hotel && props.values.hotel.rating) || "auto"}>
          <CustomTextField
            label="ציון (GOOGLE)"
            value={props.values.hotel ? props.values.hotel.rating : ""}
            disabled={props.values.hotel === undefined}
            readOnly
            loading={props.values.hotel.loading}
          />
        </Grid>
        <Grid item xs={12}>
          <PlaceImagePicker
            name="imagePick"
            key={JSON.stringify(props.values.hotel ? props.values.hotel.photos : [])}
            values={props.values}
            setFieldValue={props.setFieldValue}
            submitCount={props.submitCount}
            errors={props.errors}
          />
        </Grid>
      </Grid>
      }
    </When>
    <Grid item xs={12} style={{paddingTop: 0, marginTop: props.values.hotel !== undefined ? 20 : 0}}>
      <HotelDescription values={props.values}/>
    </Grid>
  </EditorSection>
);

HotelDetails.defaultProps = {
  values: {},
  errors: [],
  submitCount: 0,
  setFieldValue: () => null,
};

export default React.memo(HotelDetails);
