import React from "react";
import styles from "./VoucherSellerComments.module.scss";
import {IProduct} from 'data/interfaces/products';
import SellerComments from 'gui/pages/mainRouter/pages/ad/components/commons/sellerComments';
import {voucherText} from 'gui/components/listPage/components/ad/components/voucherImageSquare';
import PercentTicket from 'gui/components/icons/PercentTicket';

export interface IVoucherSellerCommentsProps {
  product: IProduct;
}


const VoucherSellerComments = (props: IVoucherSellerCommentsProps) => <SellerComments
  title={voucherText(props.product)}
  icon={PercentTicket}
  iconContainerClassName={styles.VoucherSellerComments}
  description={props.product.voucherDetails.description}
/>;

VoucherSellerComments.defaultProps = {
  product: {voucherDetails: {}},
};

export default VoucherSellerComments;
